/**
 *   Name: Bizagi Render Hand Signature Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Hand Signature render class to adjust to desktop devices
 */

// Extends from render base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.handSignature", bizagi.rendering.render.base.prototype);
// Extends from render Desktop base
bizagi.rendering.desktop.handSignature.extend("bizagi.rendering.desktop.handSignature", {
  // Statics
  BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
  BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
  BA_PAGE_CACHE: bizagi.render.services.service.BA_PAGE_CACHE },
bizagi.rendering.handSignature.prototype);
// Extends itself
bizagi.rendering.desktop.handSignature.extend("bizagi.rendering.desktop.handSignature", {}, {

  /**
                                                                                               * Returns the html templated element
                                                                                               * @returns {*}
                                                                                               */
  renderElement: function renderElement() {
    return this._super("render-base");
  },

  /**
      * Render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var self = this;
    var form = self.getFormContainer();
    var printVersion = form.params && form.params.printversion;

    // Apply plugin
    if (this.isExecutionMode()) {
      if (printVersion) {
        self.postRenderPrintVersion();
      } else {
        var $container = self.getSignatureContainer();

        // Wait until current element is rendered
        self.setResizeObserver($container, function () {
          self.renderSignature();
        });
      }
    }
  },

  postRenderPrintVersion: function postRenderPrintVersion() {
    var $container = this.getSignatureContainer();
    var imageElement = document.createElement("img");

    this.buildItemUrl().then(function (dataUrl) {
      imageElement.src = dataUrl;
      $container.append(imageElement);
    });
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();

    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) === true && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) === true && !this.hasValue() && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }

    this.triggerRenderValidate();
  },

  changeCellRequired: function changeCellRequired(argument) {
    this.changeRequired(argument);
  },

  getLabel: function getLabel() {
    return $("label", this._super());
  },

  /**
      * Add action handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var $actions = $(".bz-actions", this.getControl());
    var $container = self.getSignatureContainer();
    var autoSave = bizagi.util.debounce2(function (params) {
      if (!changed) return;

      var data = Array.prototype.slice.call(params || []);
      var dataImage = (data && data[0] || []).dataImage || "";

      self.saveDeferred = $.when(self.saveSignature(dataImage));
    }, self.DELAY_TO_SAVE);

    var changed = false;

    $container.bind("change", function () {
      changed = true;
      if ($(this).BzjSignature("isModified")) {
        self.saveTimestamp = new Date().getTime();
        var $canvas = $(".bz-signature__container-canvas", this)[0];
        var dataImage = $canvas.toDataURL("image/png");
        autoSave.fire({ dataImage: dataImage });
        self.setData(dataImage.replace(/^data:image\/(png|jpg|jpeg);base64,/, ""));
        self.properties.url = dataImage;
        self.setState(self.STATE_TYPE.done);
      }
    });

    $actions.on("click", "[data-action='clear']", function () {
      changed = true;
      autoSave.cancel();
      self.onSignatureClear($container);
    });

    $actions.on("click", "[data-action='edit']", function () {
      self.onSignatureEdit($container);
      changed = false;
    });

    $actions.on("click", "[data-action='save']", function () {
      if (!changed) {
        self.setState(self.STATE_TYPE.edit);
        $container.BzjSignature("disable");
        return;
      }

      self.onSignatureSave($container, autoSave);
      changed = false;
    });

  },

  onSignatureClear: function onSignatureClear($container) {
    var self = this;
    $container.BzjSignature("reset");

    if (self.hasValue()) {
      self.setData(self.EMPTY_VALUE);
      self.properties.url = self.EMPTY_VALUE;
      self.saveDeferred = $.when(self.deleteImage()).done(function () {
        self.setData(self.EMPTY_VALUE);
        self.properties.url = self.EMPTY_VALUE;
      }).fail(function (error) {
        self.executeFormFailHandler(error);
      });
    }

    return $.when(self.saveDeferred);
  },

  onSignatureEdit: function onSignatureEdit($container) {
    this.setState(this.STATE_TYPE.draw);
    $container.BzjSignature("enable");
  },

  onSignatureSave: function onSignatureSave($container, autoSave) {
    var self = this;

    $container.BzjSignature("disable");

    if ($container && $container.BzjSignature("isModified")) {
      var $canvas = $(".bz-signature__container-canvas", $container)[0];

      autoSave.cancel();
      self.saveSignature($canvas.toDataURL("image/png")).always(function () {
        self.setState(self.STATE_TYPE.edit);
      });
    } else {
      self.setState(self.STATE_TYPE.edit);
    }
  },


  /**
      * Apply plugin
      */
  renderSignature: function renderSignature() {
    var self = this;
    var $container = self.getSignatureContainer();
    var config = {
      sizeRatio: 3,
      cssclass: "bz-signature__container-canvas",
      color: "#000",
      background: "#FFFFFF" };


    $container.empty();
    $container.BzjSignature(config);
    $container.BzjSignature("disable");

    if (self.pluginInitialized && self.properties.url) {
      self.setSignature($container);
    } else {
      self.renderSignatureValue($container);
    }

    // Required for resize observer
    self.pluginInitialized = true;
  },

  /**
      * Set data to control
      * @param $container
      * @returns {*}
      */
  renderSignatureValue: function renderSignatureValue($container) {
    var self = this;

    return self.buildItemUrl().then(function (dataUrl) {
      if (!self.isDisposed()) {
        var url = dataUrl !== "" ? dataUrl : "";
        self.properties.url = url;

        if (!bizagi.util.isEmpty(url)) {
          $container.BzjSignature("setData", url);
        } else {
          self.setData(self.EMPTY_VALUE);
        }
      }
    });
  } });