/*
 *   Name: Bizagi Desktop Section Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the section class to adjust to desktop devices
 */

// Extends from container desktop base
bizagi.rendering.desktop.container.extend("bizagi.rendering.desktop.section", bizagi.rendering.section.prototype);
// Auto extend
bizagi.rendering.desktop.section.extend("bizagi.rendering.desktop.section", {}, {


  /**
                                                                                   * Render the trigger of the section.
                                                                                   * @returns string with html code.
                                                                                   */
  renderContainer: function renderContainer() {
    var self = this;
    var properties = this.properties;
    var template = this.renderFactory.getTemplate("section-header");

    // Render the section trigger
    var html = $.fasttmpl(template, {
      displayName: properties.displayName,
      editable: properties.editable,
      orientation: self.getOrientation(),
      uniqueId: properties.uniqueId,
      isDesign: self.getMode() !== "execution",
      messageValidation: properties.messageValidation,
      guid: properties.guid,
      displayAs: properties.displayAs });

    this.sectionContent = $(this.renderSectionContent())[0];
    return html;
  },

  /**
      * Render the content of the section.
      * @returns string with html code. 
      */
  renderSectionContent: function renderSectionContent() {
    var self = this;
    var template = this.renderFactory.getTemplate("section-content");
    var properties = this.properties;
    var breadCrumb = this.getBreadcrumb();
    this.orderSection = breadCrumb.length - 1;
    var html = $.fasttmpl(template, {
      displayName: properties.displayName,
      editable: properties.editable,
      orientation: self.getOrientation(),
      uniqueId: properties.uniqueId,
      isDesign: self.getMode() !== "execution",
      messageValidation: properties.messageValidation,
      guid: properties.guid,
      breadCrumb: breadCrumb });


    html = this.replaceChildrenHtml(html, this.renderChildrenHtml());
    return html;
  },

  /**
      * Template method to implement in each device to customize each container after processed
      * @param {} section 
      * @returns {} 
      */
  postRenderContainer: function postRenderContainer(section) {
    section.closest(".ui-bizagi-container-form-wrapper-animation").append(this.sectionContent);
    // Call base
    this._super(section);
    if (this.isActive()) this.openAutomatically();
  },

  /**
      * Template method to implement in each device to customize the container's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var container = self.container[0];
    var form = self.getFormContainer();
    var printVersion = form.params && form.params.printversion;

    var header = container.querySelector(".ui-bizagi-container-section-header");
    var breadCrumb = this.sectionContent.querySelector(".ui-bizagi-container-section-breadcrumb");
    var breadCrumbElements = breadCrumb.querySelectorAll(".ui-bizagi-container-section-breadcrumb-element");

    this.openSectionFromClick = this.openSectionFromClick.bind(this);
    if (!printVersion) {
      header.addEventListener("click", this.openSectionFromClick);
      Array.prototype.forEach.call(breadCrumbElements, function (element) {
        var stepsBack = parseInt(element.getAttribute('data-steps-back'));
        if (stepsBack !== 0)
        element.addEventListener('click', function () {
          self.goBack(stepsBack);
        });
      });
    } else {
      header.classList.add("ui-bizagi-section-print");
    }
  },

  /**
      * Execute the post render method for section's children
      * @param {*} container 
      */
  executeChildrenPostRender: function executeChildrenPostRender(container) {
    var self = this;
    var childrenElements = $(".ui-bizagi-container-section-children", this.sectionContent).children();

    $.each(childrenElements, function (i, childElement) {
      childElement = $(childElement);
      var id = Number(childElement.data("unique-id"));
      if (id) {
        var child = self.childrenHash[id];
        if (child) {
          var type = child.getElementType();
          if (type == bizagi.rendering.element.ELEMENT_TYPE_RENDER)
          child.postRenderElement(childElement);
          if (type == bizagi.rendering.element.ELEMENT_TYPE_CONTAINER)
          child.postRenderContainer(childElement);
        }
      } else {
        self.executeChildrenPostRender(childElement);
      }
    });
  },

  /**
      * Open section from a manual click
      * @param ev - ClickEvent object
      */
  openSectionFromClick: function openSectionFromClick(ev) {
    var self = this;
    var container = self.container[0];
    var containerAnimation = this.sectionContent.parentElement;
    var headerSection = container.querySelector(".ui-bizagi-container-section-header");
    headerSection.removeEventListener("click", this.openSectionFromClick);
    if (!bizagi.util.isIE() && this.getFormOrientation() !== "rtl") containerAnimation.scrollIntoView({ behavior: "smooth", block: "start" });
    this.openSection().then(function () {
      headerSection.addEventListener("click", self.openSectionFromClick);
    });
    this.setAsActive();
  },

  /**
      * Expand a section with animation
      * @return promise when the section is expanded
      */
  openSection: function openSection() {
    var defer = new $.Deferred();
    var containerAnimation = this.sectionContent.parentElement;
    var direction = this.getFormOrientation() === "rtl" ? 1 : -1;
    var sectionParent = this.getSectionParent();
    var sectionProperties = sectionParent && sectionParent.properties || {};
    var sectionParams = sectionParent && sectionParent.params || {};
    if (sectionProperties.type === "form" || sectionParams.type === "form") sectionParent = this.getFormParent(sectionParent);
    containerAnimation.classList.remove('ui-bizagi-container-form-wrapper-animation-inactive');
    containerAnimation.style.transform = 'translateX(' + 100 * this.orderSection * direction + '%)';
    setTimeout(function () {
      defer.resolve();
    }, 600);

    this.removeAnimationClasses(sectionParent.sectionContent);
    sectionParent.sectionContent.classList.add('ui-bizagi-section-animation-fade-out');
    this.restoreSectionsOutOfPath();
    this.removeAnimationClasses(this.sectionContent);
    this.sectionContent.classList.add('ui-bizagi-section-animation-fade-in');
    this.sectionContent.style.order = this.orderSection;
    this.sectionContent.style.msOrder = this.orderSection;
    return defer.promise();
  },

  /**
      * Expand a section without animation
     */
  openAutomatically: function openAutomatically() {
    this.closeAllSections();

    var sectionsToOpen = [];
    var section = this;
    var properties = section.properties || {};
    var params = section.params || {};
    var depth = this.orderSection;
    while (properties.type !== "form" && params.type !== "form") {
      sectionsToOpen.push(section);
      section = section.getSectionParent();
      properties = section.properties || {};
      params = section.params || {};
    }
    this.translateWithoutAnimation(depth);
    this.openFormWithoutAnimation();
    for (var i = sectionsToOpen.length - 1; i >= 0; i--) {
      sectionsToOpen[i].openSectionWithoutAnimation(i === 0);
    }
    this.setAsActive();
  },

  /**
      * Translate the animation container without animation
      * @param {*} depth - Depth of section to open
      */
  translateWithoutAnimation: function translateWithoutAnimation(depth) {
    var containerAnimation = this.sectionContent.parentElement;
    var direction = this.getFormOrientation() === "rtl" ? 1 : -1;
    containerAnimation.classList.add('ui-bizagi-container-form-wrapper-animation-inactive');
    containerAnimation.style.transform = 'translateX(' + 100 * depth * direction + '%)';
  },

  /**
      * Open a specific section without animation
      */
  openSectionWithoutAnimation: function openSectionWithoutAnimation(lastElement) {
    this.sectionContent.style.order = this.orderSection;
    this.sectionContent.style.msOrder = this.orderSection;
    this.removeAnimationClasses(this.sectionContent);
    this.sectionContent.classList.add('ui-bizagi-section-without-animation');
    this.sectionContent.classList.add(lastElement ? 'ui-bizagi-section-animation-fade-in' : 'ui-bizagi-section-animation-fade-out');
  },

  openFormWithoutAnimation: function openFormWithoutAnimation() {
    var section = this.getFormParent();
    section.sectionContent.style.order = section.orderSection;
    section.sectionContent.style.msOrder = section.orderSection;
    this.removeAnimationClasses(section.sectionContent);
    section.sectionContent.classList.add('ui-bizagi-section-without-animation');
    section.sectionContent.classList.add('ui-bizagi-section-animation-fade-out');
  },

  /**
      * Close all sections 
      */
  closeAllSections: function closeAllSections(treeIdentifier) {
    var form = this.getFormContainer() || {};
    var sections = form.rendersByType ? form.rendersByType["section"] : [];
    sections.forEach(function (section) {
      section.removeAnimationClasses(section.sectionContent);
      section.sectionContent.classList.add('ui-bizagi-container-section-initial-state');
      section.sectionContent.style.order = 99999;
      section.sectionContent.style.msOrder = 99999;
    });
  },

  /**
      * Restore a section and its descendants to initial state
      */
  restoreSectionsOutOfPath: function restoreSectionsOutOfPath() {
    var ancestors = [];
    var container = this.getSectionParent();
    var properties = container.properties || {};
    var params = container.params || {};
    while (properties.type !== "form" && params.type !== "form") {
      ancestors.push(properties.id);
      container = container.getSectionParent();
      properties = container.properties || {};
      params = container.params || {};
    }


    var form = this.getFormContainer() || {};
    var sections = form.rendersByType ? form.rendersByType["section"] : [];
    sections.forEach(function (section) {
      if (!ancestors.includes(section.properties.id) && section.sectionContent) {
        section.removeAnimationClasses(section.sectionContent);
        section.sectionContent.classList.add('ui-bizagi-container-section-initial-state');
        section.sectionContent.style.order = 99999;
        section.sectionContent.style.msOrder = 99999;
      }
    });
  },

  /**
      * Go back over the breadcrumb
      * @param {*} steps - Steps to go back
      */
  goBackInPath: function goBackInPath(steps) {
    var containerAnimation = this.sectionContent.parentElement;
    var section = this;
    var direction = this.getFormOrientation() === "rtl" ? 1 : -1;
    containerAnimation.classList.remove('ui-bizagi-container-form-wrapper-animation-inactive');
    for (var i = 0; i < steps; i++) {
      this.removeAnimationClasses(section.sectionContent);
      section.sectionContent.classList.add('ui-bizagi-section-animation-fade-in-out');
      section = section.getSectionParent();
      if (section.properties.type !== 'section') section = this.getFormParent();
    }
    this.removeAnimationClasses(section.sectionContent);
    section.sectionContent.classList.add('ui-bizagi-section-animation-fade-in');
    containerAnimation.style.transform = 'translateX(' + 100 * section.orderSection * direction + '%)';
    return section;
  },

  /**
      * Remove animation classes of a section
      * @param {*} DOMElement - Section container 
      */
  removeAnimationClasses: function removeAnimationClasses(DOMElement) {
    var classList = [
    'ui-bizagi-section-animation-fade-in',
    'ui-bizagi-section-animation-fade-out',
    'ui-bizagi-section-animation-fade-in-out',
    'ui-bizagi-section-hide-in-background',
    'ui-bizagi-container-section-initial-state',
    'ui-bizagi-section-without-animation'];

    classList.forEach(function (className) {
      DOMElement.classList.remove(className);
    });
  },

  getSectionParent: function getSectionParent() {
    var section = this;
    while (section.parent) {
      section = section.parent;
      var params = section.params || {};
      var properties = section.properties || {};
      if (properties.type === "section" || properties.type === "form" || params.type === "form") {
        return section;
      }
    }
    return this;
  },

  getFormParent: function getFormParent() {
    var form = this.getFormContainer();
    return {
      sectionContent: $(".ui-bizagi-main-section", form.container)[0],
      orderSection: 0,
      setAsActive: function setAsActive() {
        form.setParam("selectedSection", undefined);
      } };

  },

  /**
      * Check if the section is currently active, even after a refresh
      */
  isActive: function isActive() {
    var form = this.getFormContainer();
    var params = form.getParams() || {};
    return params.selectedSection === this.properties.id;
  },

  /**
      * Mark the section as active, and mark its parent section as inactive
      */
  setAsActive: function setAsActive() {
    var form = this.getFormContainer();
    form.setParam("selectedSection", this.properties.id);
  },

  /**
      * Shift the form to the right until the required section is visible, 
      * and then transform the invisible sections so that they are not accessible
      * @param {number} stepsBack - Levels to go back
      */
  goBack: function goBack(stepsBack) {
    var section = this.goBackInPath(stepsBack);
    section.setAsActive();
  },

  /**
      * Get the orientation of the section. If the orientation if "auto", search recursively.
      */
  getOrientation: function getOrientation() {
    var container = this;
    while (container) {
      properties = container.properties || {};
      if (properties.orientation !== "auto") return properties.orientation;
      container = container.parent;
    }
    return "auto";
  },

  /**
      * get the orientation of the form, to know in which direction to animate the movement
      */
  getFormOrientation: function getFormOrientation() {
    var form = this.getFormContainer() || {};
    var properties = form.properties || {};
    return (properties.orientation || "auto").toLowerCase();
  } });