/**
 *   Name: Bizagi Desktop queryNumber
 *   Author: UI Forms Team
 *   Comments: queryMoneyAdvanced
 *   -   This script will redefine the query text-advanced render class to adjust to desktop devices
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.queryMoneyAdvanced", bizagi.rendering.render.base.prototype);
// Extends itself
bizagi.rendering.desktop.queryMoneyAdvanced.extend("bizagi.rendering.desktop.queryMoneyAdvanced", bizagi.rendering.queryAdvanced.prototype);
// Extends itself
bizagi.rendering.desktop.queryMoneyAdvanced.extend("bizagi.rendering.desktop.queryMoneyAdvanced", {
  DATATYPE: "Currency" },
{

  // region // #region CONTROL BASE

  /**
   * Update or init the element data
   * @param data
   */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    this.isNewRenderForm = true;
    this.defaultFilter = "default";

    var properties = this.properties;
    var format = this.getResource("numericFormat");

    properties.allowDecimals = typeof properties.allowDecimals !== "undefined" ? bizagi.util.parseBoolean(properties.allowDecimals) : this.getDefaultAllowDecimals(properties.dataType);
    properties.numDecimals = properties.allowDecimals ? properties.numDecimals ? properties.numDecimals : typeof BIZAGI_DEFAULT_CURRENCY_INFO !== "undefined" ? BIZAGI_DEFAULT_CURRENCY_INFO.decimalDigits : 2 : 0;
    properties.symbol = "";
    properties.positiveFormat = format.positiveFormat;
    properties.negativeFormat = format.negativeFormat;
    properties.decimalSymbol = typeof BIZAGI_DEFAULT_CURRENCY_INFO !== "undefined" ? BIZAGI_DEFAULT_CURRENCY_INFO.decimalSeparator : format.decimalSymbol;
    properties.digitGroupSymbol = typeof BIZAGI_DEFAULT_CURRENCY_INFO !== "undefined" ? BIZAGI_DEFAULT_CURRENCY_INFO.groupSeparator : format.digitGroupSymbol;
    properties.groupDigits = this.setGroupDigits();
    properties.colorize = properties.colorizeOnNegative || false;
    properties.minValue = properties.minValue || null;
    properties.maxValue = properties.maxValue || null;
    properties.dataType = properties.dataType || 6;

    // Configures symbol
    properties.showsymbol = typeof properties.showsymbol !== "undefined" ? properties.showsymbol : true;

    if (properties.showsymbol) {
      var symbol = typeof BIZAGI_DEFAULT_CURRENCY_INFO !== "undefined" ? BIZAGI_DEFAULT_CURRENCY_INFO.symbol : format.symbol;
      properties.symbol = properties.currencySymbol ? properties.currencySymbol : symbol;
    } else {
      properties.symbol = "";
    }

    properties.groupDigits = true;

  },

  /**
      * Customize each render after processed
      */
  postRender: function postRender() {
    var filterType = this.properties.filterType || "";

    // Call to base
    this._super();

    // Call to repaint filters
    this.setFilter(filterType);
  },

  /**
      * Sets the internal value
      * @param value
      * @param triggerEvents
      */
  setValue: function setValue(value, triggerEvents) {
    triggerEvents = triggerEvents !== undefined ? triggerEvents : true;

    value = Array.isArray(value) ? JSON.encode(value) : value;

    this._super(value, triggerEvents);
  },

  /**
      * Sets the value in the rendered control
      * @param value
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;

    if (self.properties.editable)
    self.prepareForSetValue();

    var action = this.getCurrentAction();
    var newValue = self.performDataValue(value);


    // Set internal value
    this.setValue(newValue, false);

    // Update clean icon
    if (this.isExecutionMode() && action !== this.defaultFilter) {
      self.showHideCleanButton(true);
    } else {
      this.controlInitial && this.controlInitial.setDisplayValue(value);
    }

    // Render by filter type
    if (self.isCurrentTagFilter()) {
      self.addSelectedElements();
    } else if (["to", "from"].includes(action)) {
      this.controlInitial && this.controlInitial.setDisplayValue(newValue);
    } else if (value && ["between"].includes(action)) {
      var newListValue = self.performListValue(value);

      if (newListValue && newListValue.from) this.controlInitial && this.controlInitial.setDisplayValue(newListValue.from);
      if (newListValue && newListValue.to) this.controlEnd && this.controlEnd.setDisplayValue(newListValue.to);
    }
  },

  /**
      * Returns the html templated element
      * @returns {*}
      */
  renderElement: function renderElement() {
    return this._super("render-base");
  },

  /**
      *  Changes the render required
      * @param argument
      */
  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;

    this.changeRequiredLabel(argument);
  },

  /**
      * Change cell required and attach bindings
      * @param argument
      */
  changeCellRequired: function changeCellRequired(argument) {
    this.changeRequired(argument);
  },

  /**
      * This method just make a toggle of red label indicator
      * @argument {boolean} argument
      */
  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();

    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) === true && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) === true && !this.hasValue() && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }
    this.triggerRenderValidate();
  },

  /**
      * Returns the internal value
      * @returns {*|Array}
      */
  getValue: function getValue() {
    if (this.isCurrentTagFilter()) return this.performTagItems();

    if (this.isCurrentBetweenFilter()) {
      var initial = this.controlInitial ? this.formatInputValue(this.controlInitial.getValue()) : "null";
      var end = this.controlEnd ? this.formatInputValue(this.controlEnd.getValue()) : "null";
      var newValue = [initial, end];

      return initial === "null" && end === "null" ? "" : JSON.encode(newValue);
    }

    if (this.isCurrentNullFilter()) return null;

    // Call to base
    return this.controlInitial ? this.controlInitial.getValue() : "";
  },
  /**
      * Return the value on controlInitial as number
      * @param {*} value
      */
  getValidTagValue: function getValidTagValue(value) {
    return this.controlInitial && this.controlInitial.numericInput.asNumber() || $.trim(value);
  },
  /**
      * Return the group digits default property based on digitGroupSymbol
      * @returns {*}
      */
  setGroupDigits: function setGroupDigits() {
    var self = this;
    if (typeof self.properties.thousands !== "undefined") {
      if (self.properties.digitGroupSymbol) {// some cultures
        return bizagi.util.parseBoolean(self.properties.thousands);
      } else {
        return false;
      }
    } else if (self.properties.digitGroupSymbol) {// some cultures
      return this.getDefaultGroupDigits(self.properties.dataType);
    } else {
      return false;
    }
  },

  /**
      * Return the allow decimals default property based on the datatype
      * @param dataType
      * @returns {boolean}
      */
  getDefaultAllowDecimals: function getDefaultAllowDecimals(dataType) {
    if (dataType) {
      // Integer data types
      if ([1, 2, 3, 4, 7].includes(dataType)) return false;

      // Decimal and money data types
      if ([6, 8, 10, 11].includes(dataType)) return true;
    }

    return false;
  },

  /**
      * Return the group digits default property based on the datatype
      * @param dataType
      * @returns {boolean}
      */
  getDefaultGroupDigits: function getDefaultGroupDigits(dataType) {
    if (dataType) {
      // Integer data types
      if ([1, 2, 3, 4, 7].includes(dataType)) return false;

      // Decimal data types
      if ([6, 8, 10, 11].includes(dataType)) return true;
    }

    return false;
  },

  /**
      * Get the label element
      * @returns {jQuery|HTMLElement}
      */
  getLabel: function getLabel() {
    return $("label", this._super());
  },

  // endregion // #endregion

  // region // #region METHODS TO LITE CONTROLS

  /**
   * Define the controls depend of filter type
   * @param {*} params
   */
  defineLiteControl: function defineLiteControl(params) {
    var self = this;
    var typeFilter = params && params.typeFilter || "advanced";
    var numberEnd = null;
    var numberInitial = new bizagi.rendering.desktop.moneyAdvanced(self.getRenderProperties({
      typeControl: typeFilter,
      isRange: this.isCurrentBetweenFilter(),
      isMinimum: true }));


    if (this.isCurrentBetweenFilter()) {
      numberEnd = new bizagi.rendering.desktop.moneyAdvanced(self.getRenderProperties({
        typeControl: typeFilter + "_max",
        isRange: this.isCurrentBetweenFilter(),
        isMaximum: true }));


      numberInitial.subscribe(numberEnd);
      numberEnd.subscribe(numberInitial);
    }

    self.setLiteControls(numberInitial, numberEnd);
  },

  /**
      * set the rendered controls
      * @param {*} controlInitial
      * @param {*} controlEnd
      */
  setLiteControls: function setLiteControls(controlInitial, controlEnd) {
    var self = this;
    var control = self.getControl();

    var filter = this.getCurrentFilter();
    var filterType = filter && filter.action || "default";

    self.controlInitial = controlInitial || null;
    self.controlEnd = controlEnd || null;

    var template = this.renderFactory.getTemplate("query.advanced-content");
    var $templateItem = $.tmpl(template, {
      value: self.getResourceByKey(filter.label, filter.defaultLabel),
      id: filter.id,
      filterType: filterType });


    var $canvas = $(".bz-query-advanced__filter", control).empty();

    if (this.isCurrentBetweenFilter()) {
      $canvas.append($templateItem);

      var $numberMin = $(".bz-query-advanced__filter-input-from", $canvas);
      var $numberMax = $(".bz-query-advanced__filter-input-to", $canvas);

      self.renderLiteControl(filterType, $numberMin, controlInitial);
      self.renderLiteControl(filterType, $numberMax, controlEnd);

    } else {
      $canvas.append($templateItem);

      var $numberInitial = $(".bz-query-advanced__filter-input", $canvas);
      self.renderLiteControl(filterType, $numberInitial, controlInitial);
    }
  },

  /**
      * Render control
      * @param {*} filterType
      * @param {*} $canvas
      * @param {*} control
      */
  renderLiteControl: function renderLiteControl(filterType, $canvas, control) {
    var self = this;

    // Templates: input or between
    $.when(control.render(this.getTemplateByType(filterType))).
    done(function (html) {
      $canvas.empty().append(html);

      var action = self.getCurrentAction();
      var $inputFiler = $(".bz-query-advanced__input", $canvas);

      $inputFiler.attr("data-action", action);

      if (self.isCurrentNullFilter()) {
        $inputFiler.attr("readonly", true);
      }

      control.postRenderElement($canvas, true); // true for skip design hack

      if (!self.isCurrentTagFilter()) {
        self.configureInternalHandlers(control);
      }
    });
  },

  getTemplateByType: function getTemplateByType(filterType) {
    if (["between", "tags"].includes(filterType)) return "query.advanced-text";
    return "query.advanced-input";
  },

  configureInternalHandlers: function configureInternalHandlers(control) {
    var self = this;
    var $internalControl = control.getInputControl();

    // Handler for update data value
    $internalControl.bind("updateData", function (evt, params) {
      // Update data value
      self.setValue(self.getValue());
    });
  },

  getRequiredProperties: function getRequiredProperties() {
    var properties = this.properties;

    return {
      allowDecimals: properties.allowDecimals,
      numDecimals: properties.numDecimals,
      symbol: properties.symbol,
      positiveFormat: properties.positiveFormat,
      negativeFormat: properties.negativeFormat,
      decimalSymbol: properties.decimalSymbol,
      digitGroupSymbol: properties.digitGroupSymbol,
      groupDigits: properties.groupDigits,
      colorize: properties.colorize,
      minValue: properties.minValue,
      maxValue: properties.maxValue,
      dataType: properties.dataType };

  },

  // endregion // #endregion

  // region // #region METHODS TO TAGS FILTER

  addTag: function addTag(tagItem) {
    var controlTag = this.controlInitial;
    if (controlTag) {
      var numericInput = controlTag.numericInput;
      controlTag.formatNumericInput(numericInput[0]);

      //if(bizagi.util.isEmpty(controlTag.getValue()))return;

      tagItem.value = controlTag.getValue() || "";
      tagItem.displayValue = numericInput.val() || "";
    }

    this._super(tagItem);
  },

  /**
      * Add format to tag
      * @param value
      * @returns {*}
      */
  formatTagItem: function formatTagItem(value) {
    var properties = this.properties;
    var decimalFormat = {
      symbol: properties.symbol,
      positiveFormat: properties.positiveFormat,
      negativeFormat: properties.negativeFormat,
      decimalSymbol: properties.decimalSymbol,
      digitGroupSymbol: properties.digitGroupSymbol,
      groupDigits: properties.groupDigits,
      roundToDecimalPlace: properties.numDecimals,
      colorize: properties.colorize };


    return bizagi.util.formatDecimal(value, decimalFormat);
  },

  // endregion // #endregion

  // region // #region A&V METHODS

  /**
   * Changes the render minimum value
   * @param value
   */
  changeMinValue: function changeMinValue(value) {
    if (!!this.controlInitial) {
      var newValue = this.controlInitial.performMinValue(value);

      this.properties.minValue = this.properties.dataTypeMinValue = newValue;
      this.controlInitial.changeMinValue(value);

      // Condition for between filter
      if (!!this.controlEnd && !!this.controlInitial) {
        this.controlEnd.changeMinValue(value);
      }
    }
  },

  /**
      * Changes the render maximum value
      * @param value
      */
  changeMaxValue: function changeMaxValue(value) {
    if (!!this.controlInitial) {
      var newValue = this.controlInitial.performMaxValue(value);

      this.properties.maxValue = this.properties.dataTypeMaxValue = newValue;

      // Condition for between filter
      if (!!this.controlEnd && !!this.controlInitial) {
        this.controlEnd.changeMaxValue(value);
      } else {
        this.controlInitial.changeMaxValue(value);
      }
    }
  },

  /**
      * Customizes render color
      * @param {} color
      * @returns {}
      */
  changeColor: function changeColor(color) {
    var $control = this.getControl();

    // To review: Save the command to propagate action when filter is changed
    if (!!this.controlInitial) this.controlInitial.changeColor(color, true);
    if (!!this.controlEnd) this.controlEnd.changeColor(color, true);

    return this._super(color).
    changeFontColor($("label", $control), color);
  },

  /**
      * Context to apply styles
      * @param context
      * @returns {jQuery|HTMLElement}
      */
  getElementsToStylize: function getElementsToStylize(context) {
    return this._super(context);
  }

  // endregion // #endregion
});


// Extends from render desktop base
bizagi.rendering.desktop.money.extend("bizagi.rendering.desktop.moneyAdvanced", {}, {

  getControl: function getControl() {
    var self = this;
    if (!self.control || self.control instanceof jQuery && self.control.length === 0) {
      self.control = $(self.element || self.observableElement);
    }
    if (!self.control || self.control.length === 0) {
      return null;
    }
    return self.control;
  },

  subscribe: function subscribe(control) {
    this.observer = this.observer || [];
    this.observer.push(control);
  },

  notify: function notify(value) {
    if (this.properties.isRange) {
      for (var i = this.observer.length - 1; i >= 0; i--) {
        if (this.properties.isMaximumValue) {
          value = bizagi.util.isNumeric(value) ? value : this.properties.maxValue || this.calculateMaxValue(this.properties.dataType);
          this.observer[i].changeMaxValue(value);
        } else if (this.properties.isMinimumValue) {
          value = bizagi.util.isNumeric(value) ? value : this.properties.minValue || this.calculateMinValue(this.properties.dataType);
          this.observer[i].changeMinValue(value);
        }
      }
    }
  },

  setValue: function setValue(value) {
    this._super(value);
    this.notify(value);

    // Update parent control
    var $inputControl = this.getInputControl();
    $inputControl && $inputControl.trigger('updateData', { data: value });
  },

  getInputControl: function getInputControl() {
    return $("input", this.getControl()) || null;
  } });