// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.formLink", {
  BA_ACTION_PARAMETER_PREFIX: "p_",
  BA_CONTEXT_PARAMETER_PREFIX: "h_",
  BA_PAGE_CACHE: "pageCacheId" },
bizagi.rendering.formLink.prototype);
// Extends itself
bizagi.rendering.desktop.formLink.extend("bizagi.rendering.desktop.formLink", {}, {
  /**
                                                                                     * Customize the render's behaviour to add handlers
                                                                                     */
  configureHandlers: function configureHandlers() {
    this.attachEvents();
  },

  /**
      * Customize the render's behaviour
      * to add handlers when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    this.attachEvents();
  },

  /**
      * Execute the rule action
      * @param propertyName
      * @returns {*}
      */
  executeRule: function executeRule(propertyName) {
    var defer = new $.Deferred();

    this.dataService.multiaction().getPropertyData({
      url: this.properties.dataUrl,
      xpath: this.properties.xpath,
      idRender: this.properties.id,
      xpathContext: this.properties.xpathContext,
      idPageCache: this.properties.idPageCache,
      property: propertyName }).
    done(function (data) {
      if (data.type === "error") {
        // Workflow engine error
        defer.reject(data);
      } else if (data.messages != null) {
        var message = data.messages[0];
        defer.reject({ type: data.type, message: message });
      } else {
        defer.resolve(data);
      }
    }).fail(function (jqXHR, textStatus, errorThrown) {
      defer.reject({ type: "error", message: jqXHR.message });
    });
    return defer.promise();
  },

  /**
      * Validation of rule on exit
      * @returns {*}
      */
  executeExitRule: function executeExitRule(data) {
    var self = this;
    var deferred = new $.Deferred();
    if (self.properties.hasexitrule && !self.isThisFormReadOnly()) {
      return self.saveFormLinkData(data).then(function () {
        return self.executeRule("exitrule");
      });
    } else {
      deferred.resolve();
    }
    return deferred.promise();
  },

  /**
      * Execution of rule before the formLink on enter
      * @returns {*}
      */
  executeEntryRule: function executeEntryRule() {
    var deferred = new $.Deferred();

    if (this.properties.hasentryrule && !this.isThisFormReadOnly()) {
      return this.executeRule("entryrule");
    } else {
      deferred.resolve();
    }

    return deferred.promise();
  },

  /**
      * Submits a link save request for the displayed form
      * @param data
      * @returns {*} Returns a deferred
      */
  submitSaveRequest: function submitSaveRequest(data) {
    var self = this;
    var properties = self.properties;

    // Don't send request when the link is not editable
    if (!properties.editable && !properties.value) {
      return null;
    }

    var xpath = self.getFormLinkXpath();
    var xpathContext = xpath;

    if (typeof properties.xpathContext !== "undefined" && properties.xpathContext !== "") {
      xpathContext = xpath ? properties.xpathContext + "." + xpath : properties.xpathContext;
    }

    return self.dataService.saveLinkForm({
      url: properties.saveUrl,
      idRender: properties.id,
      xpathContext: xpathContext,
      submitData: data });

  },

  /**
      * Check if display is readonly
      * @returns {undefined|boolean}
      */
  isThisFormReadOnly: function isThisFormReadOnly() {
    var form = this.getFormContainer();
    return form && form.properties.displayAsReadOnly;
  },

  /**
      * Opens the link display form inside a dialog
      */
  openFormLink: function openFormLink() {
    var self = this;
    var properties = self.properties;
    var form = self.getFormContainer();

    if (typeof properties.canBeSaved == "undefined") {
      properties.canBeSaved = true;
    }

    // When the link is not editable, then the form can't have save button
    if (properties.editable === false || form.properties.displayAsReadOnly) {
      properties.canBeSaved = false;
    }

    return $.when(form.properties.editable && !self.isThisFormReadOnly() ? form.submitData() : undefined).then(function () {
      // Send edit request
      return $.when(self.submitEditRequest()).then(function () {
        return self.executeEntryRule().then(function () {
          self.openFormLinkDialog();
        }).fail(function (response) {
          self.submitRollbackRequest();
          self.handleFailRuleExecution(response);
        });
      }).fail(function () {
        console.warn("EDITRELATION action Fails");
      });
    });
  },

  /**
      * Show dialog with new form
      */
  openFormLinkDialog: function openFormLinkDialog() {
    var self = this;
    var properties = self.properties;
    var form = self.getFormContainer();

    // Show dialog with new form after that
    self.dialog = new bizagi.rendering.dialog.form(self.dataService, self.renderFactory, {
      showSaveButton: properties.canBeSaved,
      isPlanForm: properties.isPlanForm || false, //If this formLink is in a plan form
      maximized: properties.maximized,
      title: properties.displayName,
      orientation: properties.orientation || "ltr",
      onSave: function onSave(data) {
        var def = new $.Deferred();
        var deferredsToWait = null;

        if (data && data.deferreds) {
          deferredsToWait = $.when.apply($, data.deferreds);
          delete data.deferreds;
        }

        $.when(deferredsToWait).then(function () {
          self.handleOnSaveDialog(data).then(function (response) {
            def.resolve(response);
            form.refreshForm();
          }).fail(function () {
            def.reject();
          });
        });
        return def.promise();
      },
      close: function close() {
      } });


    // Open dialog
    self.dialog.render({
      idRender: properties.id,
      xpathContext: self.getFormLinkXpathContext(),
      idPageCache: properties.idPageCache,
      recordXPath: self.getFormLinkXpath(),
      requestedForm: "linkform",
      editable: properties.editable,
      url: properties.editPage,
      fromRender: self,
      mainForm: self.getMainForm(self.getFormContainer()) }).
    fail(function (data) {
      // Sends a rollback request to delete checkpoints
      $.when(self.submitRollbackRequest(data)).done(function () {
        self.submitInvalidatePageCacheRequest(data);
      });
    });
  },

  /**
      * Add an error message to the form
      * @param response
      */
  handleFailRuleExecution: function handleFailRuleExecution(response) {
    if (response.type === "alertMessages") {
      bizagi.modalError &&
      bizagi.modalError.show(response.message, "workportal-general-word-information", false);
    } else {
      var form = this.getFormContainer();
      if (response.type === "validationMessages") {
        form.validationController.showErrorMessage(response.message);
      } else {
        form.addErrorMessage(response.message);
      }
    }
  },

  /**
      * Manage the action execution on save action
      * @param data
      * @returns {*}
      */
  handleOnSaveDialog: function handleOnSaveDialog(data) {
    var self = this;

    return self.executeExitRule(data).then(function () {
      var def = new $.Deferred();
      //If has exit rule, the data has been processed previously (executeExitRule()),
      //then is only needed process the form and do a commit with the submitSaveRequest.
      var submitData = self.properties.hasexitrule ? { idPageCache: data.idPageCache } : data;
      self.submitSaveRequest(submitData).then(function (result) {
        if (result.type === "error") {
          // Workflow engine error
          def.reject(result);
        } else if (result.messages != null) {
          var message = result.messages[0];
          def.reject({ type: result.type, message: message });
        } else {
          def.resolve(result);
        }
      }).fail(function (jqXHR) {
        def.reject({ type: "error", message: jqXHR.message });
      });
      return def.promise();
    }).fail(function (response) {
      self.handleFailRuleExecution(response);
      return response;
    });
  },

  /**
      * Get context of FormLink
      * @returns {string|*}
      */
  getFormLinkXpathContext: function getFormLinkXpathContext() {
    var self = this;
    var xpathContext = self.properties.xpathContext;
    var parentXpathContext = self.getXpathContext();

    if (parentXpathContext && xpathContext) {
      if (xpathContext.indexOf(parentXpathContext) === 0) {
        return xpathContext;
      } else {
        return parentXpathContext + "." + xpathContext;
      }
    } else {
      return xpathContext;
    }
  },

  /**
      * Configure event handlers
      */
  attachEvents: function attachEvents() {
    var self = this;
    var $link = $(".ui-bizagi-render-link", self.getControl());

    // Define dialog
    self.dialog = null;

    // Bind click event
    $link.on("click", function () {
      // Open the link inside a dialog
      var $buttonLink = $(this);
      $buttonLink.prop("disabled", true);

      $.when(self.openFormLink()).always(function () {
        $buttonLink.prop("disabled", false);
      });
    });
  },

  /**
      * Customizes render color
      * @param color
      */
  changeColor: function changeColor(color) {
    var self = this;
    var control = self.getControl();
    var label = self.getLabel();

    if (control && label) {
      if (color !== "none") {
        self.getControl().find("a").css("color", color, "!important");

        if (typeof self.getControl().find("a").style == "function") {
          self.getControl().find("a").style("color", color, "!important");
        }
      } else {
        self.getControl().find("a").css("color", "");

        if (typeof self.getControl().find("a").style == "function") {
          self.getControl().find("a").style("color", "");
        }
      }
    }
    self._super(color);
  },

  /**
      * Customizes render font style
      */
  changeFontBold: function changeFontBold() {
    var self = this;
    var control = self.getControl().children();
    var textFormat = self.properties.textFormat;

    if (typeof textFormat.bold !== "undefined") {
      if (_typeof(control) == "object" && control.length > 0) {
        self.getElementsToStylize(control).css("font-weight", "bold", "!important");
      }
    }
  } });