/**
 *   Name: BizAgi Desktop Render Query MultiSelect Extension
 *   Author: Alexander Mejia
 *   Comments:
 *   -   This script will redefine the query multi select render class to adjust to desktop devices
 */

// Extends itself
bizagi.rendering.desktop.multiSelect.extend("bizagi.rendering.desktop.queryMultiSelect", {}, {

  /**
                                                                                                * Fetch the data into a deferred
                                                                                                * @returns {*[]}
                                                                                                */
  getData: function getData() {
    var properties = this.properties;

    return properties.data ? this.normalizeData(properties.data) : [];
  },
  /*
     *   Add the render data to the given collection in order to send data to the server
     */
  collectDataQueryForm: function collectDataQueryForm(renderValues) {
    var self = this;
    var properties = self.properties;
    var xpath = properties.xpath;
    var queryValue = self.mapValue(self.getValue());

    if (!bizagi.util.isEmpty(xpath) && (!bizagi.util.isEmpty(queryValue) || properties.included)) {
      var searchType = (self.properties.typeSearch || self.properties.rangeQuery || "NONE").toUpperCase();
      var newParameter = {
        "value": queryValue,
        "included": self.properties.included,
        "xpath": properties.xpath,
        "searchType": searchType,
        "orderType": "NONE",
        "renderId": self.getRenderId() };


      if (typeof properties.displayXpath !== "undefined" && properties.displayXpath !== "") {
        newParameter.displayXpath = properties.displayXpath;
      }

      renderValues.push(newParameter);
    }
  },

  mapValue: function mapValue(value) {
    var mappedValue = value || [];
    var valueObj = Array.isArray(mappedValue) ? {} : JSON.parse(value);

    if (!bizagi.util.isEmpty(value) && typeof valueObj.value !== "undefined") {
      mappedValue = !bizagi.util.isEmpty(valueObj.value) ? value : [];
    }

    return mappedValue;
  } });