/*
*   Name: Bizagi Desktop Accordion Container Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the accordion class to adjust to desktop devices
*/

// Extends from container desktop base
bizagi.rendering.desktop.container.extend("bizagi.rendering.desktop.accordion", bizagi.rendering.accordion.prototype);
// Extends itself
bizagi.rendering.desktop.accordion.extend("bizagi.rendering.desktop.accordion", {}, {

  /* 
                                                                                      *   Template method to implement in each device to customize each container after processed
                                                                                      */
  postRenderContainer: function postRenderContainer(accordion) {
    var self = this;

    // Call base
    self._super(accordion);

    // Apply accordion widget
    // Enable accordeon
    accordion.accordion({
      autoHeight: false });

  },

  /*
     *   Template method to implement in each device to customize the container's behaviour to show layout
     */
  configureLayoutView: function configureLayoutView() {
    // Do nothing
  } });