/**
 *   Name: Bizagi Desktop Render Query Money Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the query money render class to adjust to desktop devices
 */

// Extends itself
bizagi.rendering.desktop.money.extend("bizagi.rendering.desktop.queryMoney", {}, {

  /**
                                                                                    * Adds the format currency plugin
                                                                                    */
  attachFormatCurrency: function attachFormatCurrency() {
    var self = this;
    var properties = self.properties;
    if (properties.showSymbol) {

      if (properties.symbol === '' && typeof bizagi != "undefined" && properties.dataType == 8 && typeof bizagi.currentUser != "undefined") {
        if (typeof bizagi.currentUser.symbol != "undefined") {
          properties.symbol = bizagi.currentUser.symbol;
        }
      }
    }

    // Call base
    this._super();
  } });