/*
 *   Name: Bizagi Desktop Render Yes-No Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Yes-No render class to adjust to desktop devices
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.searchList", bizagi.rendering.searchList.prototype);
// Extends itself
bizagi.rendering.desktop.searchList.extend("bizagi.rendering.desktop.searchList", {
  SEARCH_MIN_LENGTH: 2 },
{

  /**
   * Template method to implement in each device to customize each render after processed
   * @returns {}
   */
  postRender: function postRender() {
    var self = this;
    var control = self.getControl();

    // Call base 
    self._super();

    // Apply plugin
    self.searchInput = $("input", control);
    $(control).find("input").css({ "width": "100%", "padding-right": "25px" });

    self.applyAutocompletePlugin();
    self.attachKeyEvents();
  },

  /**
      * Attaches key events to the control
      * @returns {}
      */
  attachKeyEvents: function attachKeyEvents() {
    var self = this;

    self.element.keydown(function (e) {
      e = window.event || e;
      var keyUnicode = e.charCode || e.keyCode;

      if (e !== undefined) {
        switch (keyUnicode) {
          case 8: // Backspace
            var caretPos = $("input:first", self.element)[0].selectionStart;

            if (caretPos === 0) {
              self.removeLastElement();
              $("li:nth-last-child(2)", self.element).remove();
              $(".biz-render-input-search", self.getControl()).focus();
            }

            break;}

      }
    });
  },

  /**
      * Apply autocomplete capabilities to a single input
      * @returns {}
      */
  applyAutocompletePlugin: function applyAutocompletePlugin() {
    var self = this;
    var control = self.getControl();
    var searchInput = self.searchInput;

    searchInput.autocomplete({
      autoFocus: true,
      messages: {
        noResults: null, //"No search results.",
        results: function results() {
        } },

      source: function source(req, add) {
        // active search
        self.activeRequestTerm = req.term;

        // Check local storage
        var now = new Date().getTime();
        var parseRequest = encodeURIComponent(req.term);
        var expireKey = self.properties.xpath + "_" + parseRequest + "_expire";
        var requestKey = self.properties.xpath + "_" + parseRequest;
        var expire = bizagi.util.getItemLocalStorage(expireKey);

        if (expire && self.properties.enableLocalStorage && now < expire) {
          try {
            var data = JSON.parse(bizagi.util.getItemLocalStorage(requestKey));
            add(data);
            self.hideAutocompleteLoading();
          } catch (e) {
            self.processRequest(req, add);
          }
        } else {
          self.processRequest(req, add);
        }
      },
      select: function select(event, ui) {
        var selectedValues = JSON.parse(self.getValue()).value;
        var isNewItem = true;
        if (typeof ui.item.id == "undefined") {
          isNewItem = false;
        } else {
          $.each(selectedValues, function (i, val) {
            if (ui.item.id == val.id) {
              isNewItem = false;
              return isNewItem;
            }
          });
        }

        if (isNewItem) {
          self.appendFoundItem(event, ui);
          self.changeRequired(false);
        }

        searchInput.autocomplete('close');
        ui.item.value = "";
      },
      open: function open() {
        var suggestMenu = searchInput.data("ui-autocomplete").menu.element;
        var suggestStyles = { top: 0, left: 0 };
        var suggestContainer = searchInput.closest(".ui-bizagi-control");
        var controlContainer = suggestContainer.parent();
        var itemWidth = self.calculateSuggestionWidth();

        $('li', suggestMenu).each(function () {
          if ($(this).index() % 2 < 1)
          $(this).addClass('biz-ui-even');
        });

        // Fix position to prevent the scroll of autocomplete
        suggestMenu.css(suggestStyles);
        suggestContainer.addClass("ac-is-visible");
        controlContainer.addClass("ac-is-visible ac-clear-floats");

        suggestMenu.appendTo(suggestContainer).position({
          my: "left top",
          at: "left bottom",
          of: this,
          collision: "flipfit" });


        suggestMenu.css("z-index", $.getMaxZindex() + 10);

        // Add data to check if element is visible
        suggestMenu.attr("visible", true);

        // Apply width to menu and items
        $(".ui-bizagi-render-search-item", suggestMenu).width(itemWidth);
        suggestMenu.width(itemWidth);
        self.hideAutocompleteLoading();

      },
      close: function close() {
        searchInput.data("ui-autocomplete").menu.element.attr("visible", false);
      },
      delay: this.Class.SEARCH_DELAY,
      minLength: self.Class.SEARCH_MIN_LENGTH });



    // Special rendering
    searchInput.data("ui-autocomplete")._renderItem = function (ul, item) {
      // Normal rendering
      return self.renderSearchItem(item).appendTo(ul);
    };

    // Set close event
    $('[data-action=remove]', control).click(function () {
      self.deleteItem($(this));
    });
  },

  /**
      * Append matched element
      * @param {} event
      * @param {} ui
      * @returns {}
      */
  appendFoundItem: function appendFoundItem(event, ui) {
    var self = this;
    var control = this.getControl();
    var ul = $('.biz-render-input-search', control).closest('ul');
    var last = $('li:last', ul);
    var newElement;

    // Format additional label
    ui.item.value = self.formatItem(ui.item.value);

    var element = self.renderFactory.getTemplate("searchListTag");

    newElement = $.fasttmpl(element,
    {
      id: ui.item.id,
      value: ui.item.value,
      editable: true },

    {
      formatItem: self.formatItem });


    // Append new li with match word
    $(last).before(newElement);

    // Set close event
    $('[data-action=remove]', ul).click(function () {
      self.deleteItem($(this));
    });

    // Set selected flag
    $('.biz-render-input-search', control).data('selected', '1');

    // Set focus on input field
    $('.biz-render-input-search', control).focus();

    // Remove selected css class from all elements
    $('li', ul).removeClass('selected');

    // Set value
    this.addElement({ id: ui.item.id, value: ui.item.value });

  },

  deleteItem: function deleteItem(element) {
    var self = this;
    var control = self.getControl();
    var li = element.parent('.bz-pill');

    // Remove from main memory and set the new value
    this.removeElement({ id: li.data('id') });

    // Remove list element
    li.remove();

    //Changing required label
    var list = JSON.parse(this.value);
    if (list.value.length === 0)
    self.changeRequired(self.properties.required);

    // Set focus on input element
    $('.biz-render-input-search', control).focus();
  },

  /* 
      *   Renders a search item
      */
  renderSearchItem: function renderSearchItem(item) {
    var self = this;
    var properties = self.properties;
    var template = self.renderFactory.getTemplate("searchItem");

    var searchItem = $.tmpl(template, {
      image: properties.searchImage,
      label: item.label,
      orientation: self.properties.orientation || "lrt" });


    searchItem.data("item.autocomplete", item);

    return searchItem;
  },

  /* 
      *   Process the request data for the autocomplete control
      */
  processRequest: function processRequest(req, add) {
    var self = this;
    var properties = self.properties;

    self.showAutocompleteLoading();
    // Create array for response objects
    var suggestions = [];

    if (req.term.length >= this.Class.SEARCH_MIN_LENGTH) {

      // Update term property to reflect the search
      properties.term = req.term;
      $.when(this.getData()).done(function (data) {
        data = data.slice(0, properties.maxRecords);

        if (data.length > 50) {
          data = data.slice(0, 50);

          suggestions.push({
            id: self.Class.NO_RESULTS_SEARCH_ID,
            label: self.getResource("render-search-maximum-records-allowed"),
            value: '' });

        } else data = data.slice(0, properties.maxRecords);
        if (data && data.length > 0) {
          $.each(data, function (i, val) {
            if (properties.maxRecords == 0 || i < properties.maxRecords) {
              var parseValue = bizagi.util.performTermMatches(val.value, req.term);

              suggestions.push({ id: val.id, label: parseValue, value: val.value });
            }
          });
        } else {
          suggestions.push({
            id: self.Class.NO_RESULTS_SEARCH_ID,
            label: self.getResource("render-grid-no-records"),
            value: '' });

        }

        addToSuggestions(suggestions, req.term);

        self.hideAutocompleteLoading();

        /*
                                         *   Method to add the special items to the suggestions
                                         */
        function addToSuggestions(allSuggestions, request) {

          // Create local storage to improve the speed
          var expireTime = new Date().getTime() + self.properties.expireCache;
          var parseRequest = encodeURIComponent(request);

          if (self.properties.enableLocalStorage) {
            var expireKey = self.properties.xpath + "_" + parseRequest + "_expire";
            var requestKey = self.properties.xpath + "_" + parseRequest;

            bizagi.util.setItemLocalStorage(expireKey, expireTime);
            bizagi.util.setItemLocalStorage(requestKey, JSON.encode(allSuggestions));
          }
          // Pass array to callback
          add(allSuggestions);
        }
      });

    }
  },

  showAutocompleteLoading: function showAutocompleteLoading() {
    $('i', this.getControl()).addClass('ui-autocomplete-loading');
  },

  hideAutocompleteLoading: function hideAutocompleteLoading() {
    $('i', this.getControl()).removeClass('ui-autocomplete-loading');
  },
  /**
      * Returns the desired width for the suggestion menu
      * @returns {}
      */
  calculateSuggestionWidth: function calculateSuggestionWidth() {
    var self = this;
    var suggestMenu = self.searchInput.data("ui-autocomplete").menu.element;

    return suggestMenu.width();
  },

  /**
      * Sets the value in the rendered control
      * @param {} value
      * @returns {}
      */
  setDisplayValue: function setDisplayValue(value) {
    if (value !== undefined) {
      if (this.properties.editable) {
        this.searchInput.val(value.label);
      }
    }
  },

  /*
      *   Returns the value to display, ex. non-editable renders
      */
  getDisplayValue: function getDisplayValue() {
    return this.selectedValue;
  },
  /*
      *   Method to render non editable values
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var self = this;
    var control = self.getControl();
    var properties = self.properties;

    var stringValue = properties.value.map(function (el) {
      return self.formatItem(el.value);
    }).join(", ");

    var tempHTML = "<label class='readonly-control'>${value}</label>";
    var templateWithValues = $.tmpl(tempHTML, { value: stringValue });

    control.empty();
    control.append(templateWithValues);
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();
    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) === true && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) === true && !this.hasValue() && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }
    this.triggerRenderValidate();
  },
  changeCellRequired: function changeCellRequired(argument) {
    this.changeRequired(argument);
  } });