/**
 *   Name: Bizagi Desktop Actions Column Decorator Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the text column decorator class to adjust to desktop devices
 */

// Extends from column
bizagi.rendering.desktop.columns.column.extend("bizagi.rendering.desktop.columns.actions", {}, {

  /*
                                                                                                  *   Post process the element after it has been rendered
                                                                                                  */
  postRender: function postRender(surrogateKey, cell) {
    // Call base
    var self = this;
    var mode = self.getMode();

    this._super(surrogateKey, cell);

    self.configureHandlersActions(surrogateKey);

    $(".ui-bizagi-render", cell).css("overflow", "auto");


    if (mode == "execution") {
      var decorated = this.getDecorated(surrogateKey);
      var control = decorated.getControl();
      // Check if its new row
      if (control.parents("tr").data("new-row")) {
        control.html(self.renderDraftControl());
      }
    }

  },

  /**
      * Configure additional Handlers
      * @param surrogateKey
      */
  configureHandlersActions: function configureHandlersActions(surrogateKey) {
    var self = this;
    var decorated = self.getDecorated(surrogateKey);
    var viewMore = decorated.element.find(".gn-bz-grid__more");
    var items = [];

    // Add selection events for the draft rows
    decorated.element.on("click", ".gn-bz-grid__delete", function (events) {
      var $context = $(this).closest("tr[data-new-row='true']");

      $context.trigger("delete-row");
    });

    if (self.grid.columnActionParams.allowDetail) {
      items.push({
        name: self.parent.properties.detailLabel,
        icon: "icon-detail",
        fn: function fn() {
          self.grid.showFormDetails(surrogateKey);
        } });

    }

    if (self.grid.columnActionParams.allowEdit && self.grid.columnActionParams.withEditForm) {
      items.push({
        name: self.parent.properties.editLabel,
        icon: "icon-edit",
        fn: function fn() {
          self.grid.editRow(surrogateKey);
        } });

    }

    if (self.grid.columnActionParams.allowDelete) {
      items.push({
        name: self.parent.properties.deleteLabel,
        icon: "icon-delete",
        fn: function fn() {
          self.grid.deleteRow(surrogateKey);
        } });

    }

    viewMore.overflowMenu({ items: items });
  },

  renderDraftControl: function renderDraftControl() {
    var self = this;
    var template = self.renderFactory.getTemplate("gridActions-delete");

    // Render template
    var html = $.fasttmpl(template, {});
    return html;
  } });