/*
*   Name: Bizagi Desktop Date Column Decorator Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the date column decorator class to adjust to desktop devices
*/

// Extends from columns desktop base
bizagi.rendering.desktop.columns.column.extend("bizagi.rendering.desktop.columns.date", bizagi.rendering.columns.date.prototype);
// Extends itself
bizagi.rendering.desktop.columns.date.extend("bizagi.rendering.desktop.columns.date", {}, {

  /*
                                                                                            *   Apply custom overrides to each decorated instance
                                                                                            */
  applyOverrides: function applyOverrides(decorated) {
    var self = this;
    decorated.originalRenderElement = decorated.renderElement;

    decorated.renderElement = function () {
      return this.originalRenderElement("render-base");
    };
    self._super(decorated);
  },

  resolveValue: function resolveValue(context, value) {
    var result = "";
    if (bizagi.util.isString(value) || bizagi.util.isNumeric(value)) {
      result = value;
    }

    if (bizagi.util.isArray(value)) {
      var indexes = context.grid.getIndexes();
      var surrogateKey = context.surrogateKey;
      var index = indexes.indexOf(surrogateKey);
      if (index !== -1) {
        result = value[index];
      }
    }
    return result;
  },

  /*
     *   Post process the element after it has been rendered
     */
  postRender: function postRender(surrogateKey, cell) {
    // Call base
    this._super(surrogateKey, cell);

    var self = this;
    var properties = self.properties;
    var decorated = this.getDecorated(surrogateKey);
    // Call base
    //decorated.properties.showTime = false;
    decorated.properties.showTime = properties.showTime ? properties.showTime : false;

    // Apply styles to input
    var input = $(".bz-input-icon", cell);
    this.applyColumnStyles(input);
  },


  /*
     *   Post process the element after it has been rendered
     */
  postRenderReadOnly: function postRenderReadOnly(surrogateKey, cell) {
    // Call base
    this._super(surrogateKey, cell);

    var self = this;
    var properties = self.properties;
    var decorated = this.getDecorated(surrogateKey);
    // Call base, but resuming the original show time property when rendering not editable
    decorated.properties.showTime = properties.showTime ? properties.showTime : false;
  } });