/**
 *   Name: Bizagi Desktop Render Yes-No Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Yes-No render class to adjust to desktop devices
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.yesno", bizagi.rendering.yesno.prototype);
// Extends itself
bizagi.rendering.desktop.yesno.extend("bizagi.rendering.desktop.yesno", {}, {

  /**
                                                                               * Template method to implement in each device to customize each render after processed
                                                                               */
  postRender: function postRender() {
    var control = this.getControl();

    // Call base 
    this._super();

    // Set control container to behave as a block
    control.addClass("ui-bizagi-render-display-block");
    control.addClass("ui-bizagi-render-boolean-yesno");

    // Remove data for cell refresh
    if (control[0]) {
      $.removeData(control[0]);
    }

    // Apply plugins
    control.optiongroup({ orientation: this.properties.orientation });
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var control = self.getControl();

    // Call base
    self._super();

    // Bind change event
    control.bind("optiongroupchange", function (evt, ui) {
      // Only change the internal value when it truly changes
      if (bizagi.util.parseBoolean(self.value) !== bizagi.util.parseBoolean(ui.value)) {
        self.setValue(ui.value);
      }
    });
  },

  /**
      * Template method to implement in each device to customize the render's behaviour
      * when rendering in design mode
      */
  configureDesignView: function configureDesignView() {
    var control = this.getControl();

    // Unbind plugin handlers
    control.find("input").unbind("click");
  },

  /**
      * Sets the value in the rendered control
      * @param {} value
      */
  setDisplayValue: function setDisplayValue(value) {
    var control = this.getControl();

    // Call base
    this._super(value);

    // Set value in control
    if (value != null && this.properties.editable) {
      var parsedValue = bizagi.util.parseBoolean(value);

      if (parsedValue != null) {
        control.optiongroup("setValue", parsedValue.toString());
      }
    }
  },

  /**
      * Cleans current value
      */
  cleanData: function cleanData() {
    var value = null;

    this.setDisplayValue(value);
    this.clearDisplayValue();
  },

  /**
      * Sets the value in the rendered control
      */
  clearDisplayValue: function clearDisplayValue() {
    var selectedItem = this.element.find(".ui-radio-state-checked");
    selectedItem.removeClass("ui-radio-state-checked");
  } });