/**
 *   Name: Bizagi Desktop Render list Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the list render class to adjust to desktop devices
 */

// Extends from render desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.radio", bizagi.rendering.combo.prototype);
// Extends itself
bizagi.rendering.desktop.radio.extend("bizagi.rendering.desktop.radio", bizagi.rendering.radio.prototype);
// Extends itself
bizagi.rendering.desktop.radio.extend("bizagi.rendering.desktop.radio", {}, {

  /**
                                                                               * Template method to implement in each device to customize each render after processed
                                                                               */
  postRender: function postRender() {
    var self = this;
    var control = self.getControl();

    // Call base 
    self._super();

    // Set control container to behave as a block
    control.addClass("ui-bizagi-render-display-block");
    control.addClass("ui-bizagi-render-boolean-yesno");

    self.properties.unique = Math.ceil(Math.random() * 1000);


    // Apply plugin
    self.radiogroup = $(".ui-bizagi-render-radio", control);
    self.radiogroup.optiongroup({ orientation: self.properties.orientation });
    if (self.selectedValue) {
      self.radiogroup.optiongroup("setValue", self.selectedValue);
    }
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    // Call base
    self._super();

    // Bind handler
    self.radiogroup.bind("optiongroupchange", function (event, ui) {
      // Updates internal value
      self.onOptionGroupChange(ui);
    });
  },

  onOptionGroupChange: function onOptionGroupChange(ui) {
    var self = this;
    var newValue = { id: ui.value, value: ui.text };
    self.selectedValue = newValue.value;
    self.setValue(newValue);
  },

  /**
      * Template method to implement in each device to customize the render's behaviour when rendering in design mode
      */
  configureDesignView: function configureDesignView() {
    var self = this;
    var control = self.getControl();

    // Unbind plugin handlers
    control.find("input").unbind("click");
  },

  /**
      * Sets the value in the rendered control
      * @param value
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    var properties = self.properties;

    if (properties.editable) {
      if (value !== undefined && value.id != "") {
        self.radiogroup.optiongroup("setValue", value.id || value.value);
      }
    }
  },

  /**
      * Sets the value in the rendered control
      */
  clearDisplayValue: function clearDisplayValue() {
    var self = this;
    var control = self.getControl();
    if (self.radiogroup) {
      if ($.data(self.radiogroup[0])['ui-optiongroup']) {
        self.radiogroup.optiongroup("setValue", 0);
      } else {
        $(control).html("<label class='readonly-control'></label>");
      }
    } else if (!self.properties.editable) {
      $(control).html("<label class='readonly-control'></label>");
    }
  },

  /**
      * Returns the selected value in the template
      * @returns {*}
      */
  getSelectedValue: function getSelectedValue() {
    return this.selectedValue;
  },

  /**
      * Customizes the font underline
      * It does not apply when we use the render.base component
      * @param underline
      * @param isLabelFormat
      */
  changeFontUnderline: function changeFontUnderline(underline, isLabelFormat) {
    var self = this;
    var textFormat = self.properties.textFormat || {};
    var valueFormat = self.properties.valueFormat || {};

    var labelStrikethrough = bizagi.util.parseBoolean(textFormat.strikethru) ? "line-through " : "";
    var valueStrikethrough = bizagi.util.parseBoolean(valueFormat.strikethru) ? "line-through " : "";
    var formatUnderline = bizagi.util.parseBoolean(underline) ? "underline" : "";

    var isLabelToFormat = typeof isLabelFormat != "undefined" || isLabelFormat != null ? isLabelFormat : true;
    var $formatElements = isLabelToFormat ? self.getFormattingLabel() : self.getElementsToStylized(self.getControl());
    var formatStrikeThrough = isLabelToFormat ? labelStrikethrough : valueStrikethrough;

    self.changeTextDecoration($formatElements, formatStrikeThrough + formatUnderline);
  },

  /**
      * Customizes the font Strikethrough property
      * It does not apply when we use the render.base component
      * @param strikeThrough
      * @param isLabelFormat
      */
  changeFontStrikethru: function changeFontStrikethru(strikeThrough, isLabelFormat) {
    var self = this;
    var textFormat = self.properties.textFormat || {};
    var valueFormat = self.properties.valueFormat || {};

    var labelUnderline = bizagi.util.parseBoolean(textFormat.underline) ? "underline " : "";
    var valueUnderline = bizagi.util.parseBoolean(valueFormat.underline) ? "underline " : "";
    var formatStrikeThrough = bizagi.util.parseBoolean(strikeThrough) ? "line-through" : "";

    var isLabelToFormat = typeof isLabelFormat != "undefined" || isLabelFormat != null ? isLabelFormat : true;
    var $formatElements = isLabelToFormat ? self.getFormattingLabel() : self.getElementsToStylized(self.getControl());

    var formatUnderline = isLabelToFormat ? labelUnderline : valueUnderline;

    self.changeTextDecoration($formatElements, formatUnderline + formatStrikeThrough);
  },

  /**
      * Context to apply styles
      * @param context
      * @returns {jQuery|HTMLElement}
      */
  getElementsToStylized: function getElementsToStylized(context) {
    return $(".ui-bizagi-render-radio label, label.readonly-control", context);
  },

  /**
      * Customizes the font Strikethrough or underline property
      * @param {} $element
      * @param {} textDecoration
      * @returns {}
      */
  changeTextDecoration: function changeTextDecoration($element, textDecoration) {
    if (textDecoration === undefined) return this;
    if ($element === undefined) return this;

    textDecoration && $element.css("text-decoration", textDecoration);

    return this;
  } });