/**
 *   Name: Bizagi Desktop Render Button Extension
 *   Author: Bizagi UI Forms
 *   Comments:
 *   -   This script will redefine the button render class to adjust to desktop devices
 */

// Extends itself
bizagi.rendering.desktop.button.extend(
"bizagi.rendering.desktop.uiPathButton", {},
{
  /**
   * Update or init the element data
   * @param {*} data 
   */
  initializeData: function initializeData(data) {
    this._super(data);
    this.properties.rpabot = this.properties.rpabot || {};
  },
  /**
      * Template method to implement in each device to customize each render after processed
      * @returns {}
      */
  postRender: function postRender() {
    this._super();

    if (this.getMode() === "execution") {
      bizagi.bzToast.init({
        zIndex: bizagi.util.getMaxZIndex() + 1 });

      this.initializeRobot();
    }
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      * @returns {}
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var button = $(":button", self.getControl());

    // Call base
    self._super();

    // Bind event
    button.on("click", function () {
      if (self.robotInstance) {
        self.runRobot();
      }
    });
  },
  /**
      * Get and init robot instance
      */
  initializeRobot: function initializeRobot() {
    var self = this;

    UiPathRobot.getProcesses().then(
    function (results) {
      if (results.length === 0) {
        var message = self.getResourceByKey("workportal-rpa-uipath-error-proccess-not-found", "UIPath processes cannot be found or are not available.");
        bizagi.showMessageBox(message, "Error");
        self.disabledButton();
        return;
      }

      // Get the ID for the sample process
      self.robotInstance = results.find(function (i) {
        return i.name.includes(self.properties.rpabot.robotName);
      });

      if (!self.robotInstance) {
        var message = self.getResourceByKey("workportal-rpa-error-bot-not-found", "bot is not found");
        bizagi.showMessageBox(self.properties.rpabot.robotName + " " + message, "Error");
        self.disabledButton();
      }
    },
    function (err) {
      var message = self.getResourceByKey("workportal-rpa-error-loading-bots", "Something went wrong,");
      bizagi.showMessageBox(message + " " + err, "Error");
      self.disabledButton();
    });

  },
  /**
      * Create toast for bot and put on queue
      */
  runRobot: function runRobot() {
    var self = this;
    self.disabledButton();
    var queueMessage = self.getResourceByKey("workportal-rpa-bot-queue", "The bot is being executed, please wait");

    if (!self.properties.toast) {
      self.properties.toast = bizagi.bzToast.addItem({
        title: self.properties.rpabot.robotName,
        content: queueMessage,
        onClickIcon: function onClickIcon(item) {
          if (item.status === "error") {
            item.relaunch().setLoaderStatus().setContent(queueMessage);
          }
        },
        onCanStartJob: function onCanStartJob() {
          self.startJob();
        } });

    } else {
      self.properties.toast.relaunch().setLoaderStatus().setContent(queueMessage);
    }
  },
  /**
      * Verify if all required inputs and outputs are in form, then execute bot task. 
      */
  startJob: function startJob() {
    var self = this;
    var toast = self.properties.toast;
    var properties = self.properties;

    var inputs = properties.rpabot.inputs || [];
    var outputs = properties.rpabot.outputs || [];

    var controlsInForm = this.areAllControlsInForm(inputs.concat(outputs));
    if (controlsInForm.allControlsFound) {
      var inputs = self.getRobotInputs();
      self.disabledButton();
      toast.setLoaderStatus();

      self.robotInstance.
      start(inputs).
      onStatus(function (status) {
        toast.setContent(status);
      }).
      then(
      function (processResults) {
        self.processOutput(processResults);

        var successMessage = self.getResourceByKey("workportal-rpa-bot-executed-succesfully", "Bot executed succesfully");
        toast.setSuccessStatus().setContent(successMessage);
        self.properties.toast = undefined;
        self.enableButton();
      },
      //error function
      function (err) {
        var message = err.InnerException ? err.InnerException.Message : self.getResourceByKey("workportal-rpa-bot-error-executed", "There is an error in the bot execution");
        toast.setErrorStatus().setContent(message);
        self.enableButton();
      });

    } else {
      self.handleNotFoundControls(controlsInForm.controlsNotFound);
    }
  },
  /**
      * Set the getted value from output robot in control with xpath in form.
      * @param {*} robotResult 
      */
  processOutput: function processOutput(robotResult) {
    var form = this.getFormContainer();
    var outputs = this.properties.rpabot.outputs || [];

    outputs.forEach(function (output) {
      var responseValue = robotResult[output.robotArgumentName];
      if (responseValue) {
        var relatedControl = form.getRenders(output.xpath)[0];
        if (!bizagi.util.isEmpty(relatedControl)) {
          relatedControl.setDisplayValue(responseValue);
        }
      }
    });
  },
  /**
      * Disabled button for user can't click it.
      */
  disabledButton: function disabledButton() {
    var input = $("input", this.getControl());
    input.attr("disabled", true).addClass("ui-state-disabled rpa-disabled");

  },
  /**
      * Enavle button for user can click it.
      */
  enableButton: function enableButton() {
    var input = $("input", this.getControl());
    input.removeAttr("disabled").removeClass("ui-state-disabled rpa-disabled");
  },
  /**
      * Return a object with required inputs and their values.
      */
  getRobotInputs: function getRobotInputs() {
    var form = this.getFormContainer();
    var args = {};
    var inputs = this.properties.rpabot.inputs || [];

    inputs.forEach(function (input) {
      var relatedControl = form.getRenders(input.xpath)[0];
      var value = "";
      if (!bizagi.util.isEmpty(relatedControl)) {
        value = relatedControl.getValue();
      }
      args[input.robotArgumentName] = value;
    });

    return args;
  },
  /**
      * Return if the controls in parameters are in form. If any control isn't form return the control's xpath.
      * @param {*} rpaArgs 
      */
  areAllControlsInForm: function areAllControlsInForm(rpaArgs) {
    var form = this.getFormContainer();
    var resp = {
      allControlsFound: true,
      controlsNotFound: [] };

    rpaArgs = rpaArgs || [];
    rpaArgs.forEach(function (arg) {
      var relatedControl = form.getRenders(arg.xpath)[0];
      if (bizagi.util.isEmpty(relatedControl)) {
        resp.allControlsFound = false;
        resp.controlsNotFound.push(arg.xpath);
      }
    });
    return resp;
  },
  /**
      * Show modal message when any required input or output aren't in form.
      * @param {*} controls 
      */
  handleNotFoundControls: function handleNotFoundControls(controls) {
    var message = this.getResourceByKey("workportal-rpa-controls-not-found", "Controls associated with the bot’s inputs and outputs are not within the form");
    this.properties.toast.setErrorStatus().setContent(message);

    message += ":";
    controls.forEach(function (i) {
      message += i + ",\n";
    });
    bizagi.showMessageBox(message, "Error");

    this.disabledButton();
  } });