    var containerControl = control.closest(".ui-bizagi-control");
    var containerRender = containerControl.closest(".ui-bizagi-render");
    var idDropdown = "" + dropDown.attr("id");
    self.inputCombo.attr("aria-expanded", "false");

    // fix for SUITE-9507
    if (containerControl.hasClass("ac-is-visible")) containerControl.removeClass("ac-is-visible");
    if (containerControl.hasClass("ac-clear-floats")) containerControl.removeClass("ac-clear-floats");
    if (containerRender.hasClass("ac-is-visible")) containerRender.removeClass("ac-is-visible");

    dropDown.remove();

