/*
 *   Name: Bizagi Render ComboSelected class
 *   Author: Paola Herrera
 *   Comments:
 *   -   This script will define basic stuff for comboSelected renders
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.comboSelected", bizagi.rendering.comboSelected.prototype);
// Extends itself
bizagi.rendering.desktop.comboSelected.extend("bizagi.rendering.desktop.comboSelected", {}, {

  /*
                                                                                              * Template method to implement in each device to customize each render after processed
                                                                                              */
  postRender: function postRender() {
    // Call base
    this._super();
    var control = this.getControl();

    // Value data into selectedItemsToUpdate
    this.showInitialData(this.properties.value);

    //Show initial value or nothing
    if (this.selectedItemsToUpdate.length >= 0) {
      $.each(this.selectedItemsToUpdate, function (index, value) {
        $("#listBoxComboData", control).append("<option value='" + value.id + "'>" + value.value + " </option>");
      });
    }

    $(control).closest(".ui-bizagi-render-control").addClass("ui-bizagi-render-comboselected");
  },

  orderArrayList: function orderArrayList() {
    var arrayOrderList = this.properties.value;
    var orderList = arrayOrderList.sort(function (a, b) {

      var nA = a.value !== undefined && a.value !== null ? a.value.toLowerCase() : "";
      var nB = b.value !== undefined && b.value !== null ? b.value.toLowerCase() : "";

      if (nA < nB)
      return -1;else
      if (nA > nB)
      return 1;
      return 0;
    });
    this.properties.value = orderList;
  },

  /*
     * Template method to implement in each device to customize the render's behaviour to add handlers
     */
  configureHandlers: function configureHandlers() {
    var self = this;
    var control = self.getControl();

    //Event to move right the elements
    $("#move_right", control).click(function (e) {
      self.addItem();
      var input = $("#bz-render-comboSelected-listBoxResults .ui-bizagi-render-text", control);
      self.filterFunction(input.val(), self.rightValues, $("#bz-render-comboSelected-listBoxResults select", control));
      input.focus();
    });

    //Event to move left the elements
    $("#move_left", control).click(function (e) {
      self.removeItem();
      var input = $("#bz-render-comboSelected-selectOptions .ui-bizagi-render-text", control);
      self.filterFunction(input.val(), self.leftValues, $("#bz-render-comboSelected-selectOptions select", control));
      input.focus();
    });

    $("#bz-render-comboSelected-selectOptions .ui-bizagi-render-text", control).bind("keyup input", function (e) {
      self.filterFunction(this.value, self.leftValues, $("#bz-render-comboSelected-selectOptions select", control));
    });

    $("#bz-render-comboSelected-listBoxResults .ui-bizagi-render-text", control).bind("keyup input", function (e) {
      self.filterFunction(this.value, self.rightValues, $("#bz-render-comboSelected-listBoxResults select", control));
    });
  },

  filterFunction: function filterFunction(filter, list, selectInput) {
    $(selectInput).empty();
    //Add the values that match
    var results = list.reduce(function (val, el) {
      if (el.value.toUpperCase().indexOf(filter.toUpperCase()) !== -1) {
        val += "<option value='" + el.id + "'>" + el.value + " </option>";
      }
      return val;
    }, "");
    selectInput.append(results);
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();

    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) == true && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) == true && !this.hasValue() && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }

    this.triggerRenderValidate();
  },

  /*
     * Override display value
     */
  setDisplayValue: function setDisplayValue(value) {
    // Set internal value
    this.setValue(value, false);
  },

  /*
     * Add item to listBoxComboData and selectedItemsToUpdate array
     */
  addItem: function addItem() {
    var self = this;
    var control = self.getControl();
    var dataSelected = $("#selectionData option:selected", control);
    var dataResults = $("#listBoxComboData", control);
    $.each(dataSelected, function (i, item) {
      var newItem = {
        id: parseInt(item.value),
        value: item.text };

      var inArray = $.grep(self.selectedItemsToUpdate, function (e) {
        return e.id == newItem.id;
      });
      if (inArray.length <= 0) {
        self.selectedItemsToUpdate.push(newItem);

        self.rightValues.push(newItem);
        self.leftValues = $.grep(self.leftValues, function (el) {
          return el.id != newItem.id;
        });
      }
    });
    dataSelected.remove();
    self.setValue(self.selectedItemsToUpdate);
  },

  /*
     * Remove item from listBoxComboData and selectedItemsToUpdate array
     */
  removeItem: function removeItem() {
    var self = this;
    var control = self.getControl();

    var dataSelectedToRemove = $("#listBoxComboData option:selected", control);
    var dataOptions = $("#selectionData", control);

    $.each(dataSelectedToRemove, function (i, item) {
      var removeItem = {
        id: parseInt(item.value),
        value: item.text };

      var inArray = $.grep(dataOptions, function (e) {
        return e.id == removeItem.id;
      });
      if (inArray.length <= 0) {
        self.leftValues.push(removeItem);
        self.rightValues = $.grep(self.rightValues, function (e) {
          return e.id != removeItem.id;
        });

        self.selectedItemsToUpdate = $.grep(self.selectedItemsToUpdate, function (e) {
          return e.id != removeItem.id;
        });
      }
    });

    dataSelectedToRemove.remove();
    self.setValue(self.selectedItemsToUpdate);
  },

  /*
     * Show listBoxComboData and move_left button from control view
     */
  showListBoxComboData: function showListBoxComboData() {
    var control = this.getControl();
    $("#move_left", control).show();
    $("#bz-render-comboSelected-listBoxResults", control).show();
  },

  /*
     *   Sets the value in the rendered control
     */
  showInitialData: function showInitialData(val) {
    var self = this;
    self.orderArrayList();
    self.selectedItemsToUpdate = [];

    if (!!val) {
      $.each(val, function (index, value) {
        self.selectedItemsToUpdate.push({
          id: value.id,
          value: value.value });

      });
    }
  },

  renderReadOnly: function renderReadOnly() {
    var htmlControl = this.renderFactory.getTemplate("render-comboSelectedReadOnly");
    this.showInitialData(this.properties.value);
    return $.fasttmpl(htmlControl, { datasource: this.selectedItemsToUpdate });
  },

  /*
     * Has Value
     */
  hasValue: function hasValue() {
    var data = typeof this.value === "string" ? JSON.parse(this.value).value : this.value;
    return !bizagi.util.isEmpty(data);
  },

  /*
     *   Sets the internal value
     */
  setValue: function setValue(value, triggerEvents) {
    // Set previous value
    this.properties.previousValue = this.properties.originalValue = JSON.stringify({ "value": this.value });

    // Change internal value
    this.value = this.properties.value = JSON.stringify({ "value": value });

    if (this.properties.required && this.getMode() == "execution") {

      if (this.checkRequired([])) {
        this.changeRequiredLabel(false);
      } else {
        this.changeRequiredLabel(true);
      }
    }

    triggerEvents = triggerEvents !== undefined ? triggerEvents : true;
    // Trigger events
    if (triggerEvents && !this.internalSetInitialValueFlag) {
      this.triggerRenderChange();
      this.triggerRenderValidate();
    }
  } });