/**
 *   Name: Bizagi Desktop Render Cascading combo Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the cascading combo render class to adjust to desktop devices
 */

// Extends from render desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.cascadingCombo", bizagi.rendering.combo.prototype);
// Extends itself
bizagi.rendering.desktop.cascadingCombo.extend("bizagi.rendering.desktop.cascadingCombo", bizagi.rendering.cascadingCombo.prototype);
// Extends itself
bizagi.rendering.desktop.cascadingCombo.extend("bizagi.rendering.desktop.cascadingCombo", {
  // Statics
  BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
  BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX },
{

  /*
  *   Template method to implement in each device to customize each render after processed
  */
  postRender: function postRender() {
    var self = this;

    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.postRender.apply(self, arguments);
    self.processLayout(false, self.properties.valueFormat || {});
  },

  /*
     *   Template method to implement in each device to customize the render's behaviour to add handlers
     */
  configureHandlers: function configureHandlers() {
    // Call base
    this._super();

    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.configureHandlers.apply(this, arguments);
  },

  comboDropDown: function comboDropDown() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.comboDropDown.apply(this, arguments);
  },

  showLoadingData: function showLoadingData() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.showLoadingData.apply(this, arguments);
  },

  hideLoadingData: function hideLoadingData() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.hideLoadingData.apply(this, arguments);
  },

  internalComboDropDown: function internalComboDropDown() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.internalComboDropDown.apply(this, arguments);
  },

  resizeCombo: function resizeCombo() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.resizeCombo.apply(this, arguments);
  },

  validateValueInDatasource: function validateValueInDatasource() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.validateValueInDatasource.apply(this, arguments);
  },

  /*
     *   Keydown listener
     */
  keyDownFunction: function keyDownFunction(e) {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.keyDownFunction.apply(this, arguments);
  },

  /*
     *   KeyUp listener
     */
  keyUpFunction: function keyUpFunction(e) {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.keyUpFunction.apply(this, arguments);
  },

  /*
     * Select letter by letter
     */
  selectItemByKeyUp: function selectItemByKeyUp() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.selectItemByKeyUp.apply(this, arguments);
  },

  selectFirstElement: function selectFirstElement() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.selectFirstElement.apply(this, arguments);
  },

  selectLastElement: function selectLastElement() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.selectLastElement.apply(this, arguments);
  },

  /*
     * Handler key Up
     */
  selectPreviousElement: function selectPreviousElement() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.selectPreviousElement.apply(this, arguments);
  },
  /*
     *Handler key Down
     */
  selectNextElement: function selectNextElement() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.selectNextElement.apply(this, arguments);
  },

  /*
     *   Sets the value in the rendered control
     */
  setDisplayValue: function setDisplayValue() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.setDisplayValue.apply(this, arguments);
  },

  /*
     *   Sets the value in the rendered control
     */
  clearDisplayValue: function clearDisplayValue() {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.clearDisplayValue.apply(this, arguments);
  },

  /*
     *   Returns the selected value in the template
     */
  getSelectedValue: function getSelectedValue() {
    // Call desktop.combo method
    return bizagi.rendering.desktop.combo.prototype.getSelectedValue.apply(this, arguments);
  },

  /*
     *   Handler to react when a combo item is selected
     */
  onComboItemSelected: function onComboItemSelected() {
    this.itemSelected = true;
    // Call desktop.combo method
    return bizagi.rendering.desktop.combo.prototype.onComboItemSelected.apply(this, arguments);
  },

  postRenderPrintVersion: function postRenderPrintVersion() {
    var control = this.getControl();

    control.empty();
    control.append(this.value.value);
  },

  /*
     * validate when fire event form document and window
     */
  dropDownValidClose: function dropDownValidClose() {
    return bizagi.rendering.desktop.combo.prototype.dropDownValidClose.apply(this, arguments);
  },

  /*
     * set a new dropdown position
     */
  dropDownReposition: function dropDownReposition(dropDown, containerForm) {
    return bizagi.rendering.desktop.combo.prototype.dropDownReposition.apply(this, arguments);
  },

  /*
     * destroy dropdown
     */
  dropDownDestroy: function dropDownDestroy(dropDown) {
    return bizagi.rendering.desktop.combo.prototype.dropDownDestroy.apply(this, arguments);
  },

  /*
     *   Find elements within a data source
     */
  findDataById: function findDataById() {
    return bizagi.rendering.desktop.combo.prototype.findDataById.apply(this, arguments);
  },

  findDataByValue: function findDataByValue() {
    return bizagi.rendering.desktop.combo.prototype.findDataByValue.apply(this, arguments);
  },

  recalculateComboOffset: function recalculateComboOffset() {
    return bizagi.rendering.desktop.combo.prototype.recalculateComboOffset.apply(this, arguments);
  },
  /**
      * Check if dialog is preferences view
      */
  isPreferencesDialog: function isPreferencesDialog(container) {
    return bizagi.rendering.desktop.combo.prototype.isPreferencesDialog.apply(this, arguments);
  },

  setValue: function setValue(val, triggerEvents) {
    var self = this;
    triggerEvents = triggerEvents !== undefined ? triggerEvents : true;

    if (val === '') {
      val = { id: "", value: "" };
    }

    if (!!val) {
      var id = val.id || "";
      var label = val.value || self.findDataById(id).value;

      self.selectedValue = { id: id, value: label };

      if (!self.internalSetInitialValueFlag) {
        if (self.value === null || self.value.id !== val.id) {
          self.triggerHandler("select", [{ id: id, value: label }, false]);
        }
      }

      this._super(self.selectedValue, triggerEvents);
    }
  },

  /*
      * Cleans current data
      */
  cleanData: function cleanData() {
    var self = this;

    self.value = self.properties.value = self.selectedValue = { id: "", value: "" };
    self.setValue(self.value, false);
    self.setDisplayValue(self.value);
    self.clearDisplayValue();
  },

  changeRequired: function changeRequired(argument) {
    return bizagi.rendering.desktop.combo.prototype.changeRequired.apply(this, arguments);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    return bizagi.rendering.desktop.combo.prototype.changeRequiredLabel.apply(this, arguments);
  },

  changeRequiredControl: function changeRequiredControl(argument) {
    return bizagi.rendering.desktop.combo.prototype.changeRequiredControl.apply(this, arguments);
  },

  //#region Format

  /**
   * Customizes the font underline
   * It does not apply when we use the render.base component
   * @param underline
   * @param isLabelFormat
   */
  changeFontUnderline: function changeFontUnderline(underline, isLabelFormat) {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.changeFontUnderline.apply(this, arguments);
  },

  /**
      * Customizes the font Strikethrough property
      * It does not apply when we use the render.base component
      * @param strikeThrough
      * @param isLabelFormat
      */
  changeFontStrikethru: function changeFontStrikethru(strikeThrough, isLabelFormat) {
    // Call desktop.combo method
    bizagi.rendering.desktop.combo.prototype.changeFontStrikethru.apply(this, arguments);
  },

  /**
      * Context to apply styles
      * @param context
      * @returns {jQuery|HTMLElement}
      */
  getElementsToStylized: function getElementsToStylized(context) {
    // Call desktop.combo method
    return bizagi.rendering.desktop.combo.prototype.getElementsToStylized.apply(this, arguments);
  },

  /**
      * Customizes the font Strikethrough or underline property
      * @param {} $element
      * @param {} textDecoration
      * @returns {}
      */
  changeTextDecoration: function changeTextDecoration($element, textDecoration) {
    // Call desktop.combo method
    return bizagi.rendering.desktop.combo.prototype.changeTextDecoration.apply(this, arguments);
  }

  //#endregion Format
});

// We override original select menu refresh position method to adjust custom stuff
var originalRefreshPositionMethod = $.ui.selectmenu.prototype._refreshPosition;
$.ui.selectmenu.prototype._refreshPosition = function () {
  // Set initial height to zero
  this.list.height(0);

  // Call original method
  var result = originalRefreshPositionMethod.apply(this, arguments);

  // Return original response
  this.list.css("width", this.newelement.width() + "px");
  return result;
};