/*
*   Name: Bizagi Desktop AccordionItem Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the accordion item class to adjust to desktop devices
*/

// Extends from container desktop base
bizagi.rendering.desktop.container.extend("bizagi.rendering.desktop.accordionItem", bizagi.rendering.accordionItem.prototype);
// Extends itself
bizagi.rendering.desktop.accordionItem.extend("bizagi.rendering.desktop.accordionItem", {}, {

  /* 
                                                                                              *   Change selected item 
                                                                                              */
  setAsActiveContainer: function setAsActiveContainer(argument) {
    var self = this;

    $.when(self.ready()).
    done(function () {
      var accordionContainer = self.parent;

      // Changes item
      accordionContainer.container.accordion("option", "active", self.properties.ordinal);
    });
  },

  /* 
     *   Focus on container
     */
  focus: function focus() {
    var self = this;

    self.activate();
    self.setAsActiveContainer();

    // Call base
    this._super();
  } });