/**
 * Name: Bizagi Desktop Render Template Money Extension
 * Author: Andres Fernando Munoz
 * Comments:
 * - This script will redefine the money render class inside templates to adjust to desktop devices
 */

// Extends from layoutRender desktop base
bizagi.rendering.desktop.layoutRender.extend("bizagi.rendering.desktop.layoutMoney", bizagi.rendering.layoutNumber.prototype);
// Extends itself
bizagi.rendering.desktop.layoutMoney.extend("bizagi.rendering.desktop.layoutMoney", bizagi.rendering.layoutMoney.prototype);
// Extends itself
bizagi.rendering.desktop.layoutMoney.extend("bizagi.rendering.desktop.layoutMoney", {}, {

  /**
                                                                                           * Sets the value in the rendered control
                                                                                           */
  setValue: function setValue(val, triggerEvents) {
    var self = this;
    var control = self.getControl();

    if (self.getMode() === "execution") {
      val = val === null || val === "" ? "" : self.getDisplayValue(val);
    }
    self.properties.displayName = val;

    this._super(val, triggerEvents);
    if (control) {
      $(".ui-bizagi-render-number", control).text(val);
    }
  } });