/*
*   Name: Bizagi Desktop Horizontal Container Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the horizontal container class to adjust to desktop devices
*/

// Extends from container desktop base
bizagi.rendering.desktop.container.extend("bizagi.rendering.desktop.horizontal", bizagi.rendering.horizontal.prototype);
// Auto extend
bizagi.rendering.desktop.horizontal.extend("bizagi.rendering.desktop.horizontal", {}, {

  /* 
                                                                                        *   Template method to implement in each device to customize each container after processed
                                                                                        */
  postRenderContainer: function postRenderContainer(container) {
    var self = this;
    var children = self.children;
    var iCount = children.length;

    // Call base
    self._super(container);

    // Process each child
    $.each(children, function (i, child) {
      // Process width
      var renderWidth = child.properties.width;
      if (renderWidth == null || renderWidth.length == 0) {
        // Set default size
        renderWidth = 100 / iCount;
      } else {
        // Set custom size
        renderWidth = bizagi.util.percent2Number(renderWidth);
      }

      (function (j, innerChild, jCount) {
        $.when(innerChild.ready()).done(function () {
          var renderedElement = innerChild.getRenderedElement();
          // Set width
          if (j != jCount - 1) {
            renderedElement.css({ width: renderWidth + "%" });
          } else {
            renderedElement.css({ width: renderWidth + "%" });
          }

          renderedElement.addClass('ui-panel-area');

          // Set class for last render
          if (j == jCount - 1) {
            renderedElement.addClass("ui-bizagi-render-last");
          }
        });
      })(i, child, iCount);
    });
  } });