/**
 *   Name: Bizagi Desktop Column Decorator Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the column decorator class to adjust to desktop devices
 */

// Extends from columns desktop base
bizagi.rendering.desktop.columns.column.extend("bizagi.rendering.desktop.columns.search", bizagi.rendering.columns.search.prototype);
// Extends itself
bizagi.rendering.desktop.columns.search.extend("bizagi.rendering.desktop.columns.search", {}, {

  /*
                                                                                                *   Post process the element after it has been rendered
                                                                                                */
  postRender: function postRender(surrogateKey, cell) {
    // Call base
    this._super(surrogateKey, cell);

    var decorated = this.getDecorated(surrogateKey);

    // Apply styles to input
    var input = $(".ui-selectmenu-status", decorated.getControl());
    this.applyColumnStyles(input);

    // Remove padding for td
    if (this.properties.editable) {
      decorated.getControl().addClass("ui-bizagi-cell-search");
    }
  },

  /*
     *   Post process the element after it has been rendered
     */
  postRenderReadOnly: function postRenderReadOnly(surrogateKey, cell, tableCell) {
    // Call base
    this._super(surrogateKey, cell);

    // Apply styles to input
    var input = $(".ui-selectmenu-status", cell);
    this.applyColumnStyles(input);
  },

  /**
      * Apply custom overrides to each decorated instance
      * @param decorated
      */
  applyOverrides: function applyOverrides(decorated) {
    this._super(decorated);

    decorated.refreshControl = this.refreshControl;

    // Only apply to advanced search control
    if (decorated.properties.advancedSearch) {
      var originalRenderCellControl = decorated.internalRenderCellControl;

      // Override to base function
      decorated.internalRenderCellControl = function (editable, key, renderColumn, decorated) {
        var self = this;

        // Set editable
        var originalEditable = renderColumn.properties.editable;
        renderColumn.properties.editable = editable;

        $.when(renderColumn.render(key, decorated.getValue(key))).done(function (cell) {

          // Update cell content
          self.element && self.element.find("[data-bizagi-component=cellContent] > div").replaceWith(cell);

          // Call to base
          originalRenderCellControl.call(self, editable, key, renderColumn, decorated);
        });

        // Restore editable
        renderColumn.properties.editable = originalEditable;

      };
    }
  },

  refreshControl: function refreshControl() {
    this.grid.refreshCell({
      key: this.surrogateKey,
      column: this.properties.xpath,
      xpathContext: this.getFormContainer().properties.xpathContext });

  } });