// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.getUser", bizagi.rendering.getUser.prototype);
// Extends itself
bizagi.rendering.desktop.getUser.extend("bizagi.rendering.desktop.getUser", {}, {

  /**
                                                                                   * Template method to implement in each device to customize each render after processed
                                                                                   */
  postRender: function postRender() {
    var self = this;
    var opt = {};
    self.input = $(".ui-bizagi-render-text", self.getControl());

    if (self.properties.value != null && self.properties.value.length > 0) {
      self.setDisplayValue(self.properties.value[0]);
    }
    // Call base
    this._super();
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var control = self.getControl();
    self.bindSearchUserPlugin(control);
    $('#btn-clear-user', control).on('click', function () {
      self.clearValue();
    });
  },

  /**
      * Set display value
      */
  setDisplayValue: function setDisplayValue(data, triggerEvents) {
    var self = this;
    triggerEvents = triggerEvents == false ? triggerEvents : true;
    var value = typeof data !== "undefined" ? data.value : null;

    if (self.properties.editable) {
      self.input.val(value);
    }

    self.setValue(data, triggerEvents);
  },

  /**
      * Clear data control
      */
  clearValue: function clearValue() {
    var self = this;

    if (self.input) {
      self.setDisplayValue("", false);
    }
  },

  /**
      * Binds the search user plugin
      */
  bindSearchUserPlugin: function bindSearchUserPlugin(control) {
    var self = this;
    var options = {
      title: self.getResource("render-plugin-search-users-title"),
      maximize: false,
      filters: ["domain", "userName", "fullName"],
      displayNames: [
      self.getResource("render-plugin-search-users-domain"),
      self.getResource("render-plugin-search-users-user-name"),
      self.getResource("render-plugin-search-users-full-name")],
      searchButtonName: self.getResource("render-plugin-search-users-search-button-name"),
      clearButtonName: self.getResource("render-plugin-search-users-clear-button-name"),
      valuesToSelect: ["iduser", "user"], //"iduser","username","user","domain","email"
      renderFactoryReference: self.renderFactory,
      getResourceReference: self.getResource.bind(self),
      selectItem: function selectItem() {
        var deferred = $.Deferred();
        deferred.done(function (data) {
          self.onItemSelectedSuccess(data);
        }).fail(function (error) {
          bizagi.log(error);
        });
        return deferred;
      },
      searchUsers: function searchUsers(filterData) {
        return self.callSearchUserEndpointByContext(self.properties, filterData);
      },
      showError: function showError(error) {
        bizagi.log(error);
      },
      showMessageBox: function showMessageBox(message) {
        bizagi.showMessageBox(bizagi.localization.getResource(message), "Bizagi", "warning");
      } };


    if (self.properties.allowDelegatedUser) {
      options.editingUser = self.properties.idUser;
    }

    opt = options;

    // Allows add new user when creating the pluggin
    var params = self.getParams();
    if (typeof params.isEntityForm !== "undefined" && params.isEntityForm) {
      options.allowAddUser = bizagi.menuSecurity.UserAdmin || false;
      options.dataService = self.dataService;
      options.renderFactory = self.renderFactory;
    }

    $('#btn-get-user', control).bizagiSearchUser(options);
  },

  /**
      * Call the search users endpoint depending on the scope
      * @param {*} properties 
      * @param {*} filterData 
      */
  callSearchUserEndpointByContext: function callSearchUserEndpointByContext(properties, filterData) {

    var self = this;
    if (self.getFormContainer().params.type === "usersForm") {
      return self.callSearchUserEndpointInUsersForm(properties, filterData);
    }

    var form = self.getFormContainer();
    if (form.params.context === "userPreferencesForm") {
      return self.callSearchUserEndpointInUserPreferencesForm(properties, filterData);
    }

    // default behavior
    return self.callSearchUserEndpointInOtherCases(properties, filterData);
  },

  /**
      * Call search user endpoint depending on xpath in Users Form
      * @param {*} properties 
      * @param {*} filterData 
      */
  callSearchUserEndpointInUsersForm: function callSearchUserEndpointInUsersForm(properties, filterData) {
    var self = this;
    if (properties.xpath === "idBossUser") {
      return self.dataService.getUsersListForXpathIdBossUser(filterData);
    }

    if (self.xpathIsDelegated(properties)) {
      return self.dataService.getUsersListForXpathIdDelegate(filterData);
    }

    // if any call happens, call by default the next:
    return self.callSearchUserEndpointInOtherCases(properties, filterData);
  },

  /**
      * Call search user endpoint depending on xpath in Preferences Form
      * @param {*} properties 
      * @param {*} filterData 
      */
  callSearchUserEndpointInUserPreferencesForm: function callSearchUserEndpointInUserPreferencesForm(properties, filterData) {
    var self = this;
    if (self.xpathIsDelegated(properties)) {
      return self.dataService.getUsersListForXpathIdDelegate(filterData);
    }
  },

  /**
      * Identify if the xpath is delegate
      * @param {*} properties 
      */
  xpathIsDelegated: function xpathIsDelegated(properties) {
    return properties.xpath === "idDelegate";
  },

  /**
      * Call search users endpoint depending on other cases
      * @param {*} properties 
      * @param {*} filterData 
      */
  callSearchUserEndpointInOtherCases: function callSearchUserEndpointInOtherCases(properties, filterData) {
    var self = this;
    if (properties.allowDelegatedUser) {
      return self.dataService.getUsersListFromPreferences(filterData);
    } else {
      return self.dataService.getUsersListForStakeholders(filterData);
    }
  },


  /**
      * returns the opt var for unit testing
      */
  getOptions: function getOptions() {
    return opt;
  },
  /**
      *
      * @param itemSelected
      */
  onItemSelectedSuccess: function onItemSelectedSuccess(itemSelected) {
    var self = this;
    var data = {
      id: itemSelected.iduser,
      value: itemSelected.user };


    self.setDisplayValue(data);
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();

    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) == true && this.properties.editable) {
          control.addClass('control-required');
        } else {
          control.removeClass('control-required');
        }
      } else
      {
        if (bizagi.util.parseBoolean(argument) == true && !this.hasValue() && this.properties.editable) {
          control.addClass('control-required');
        } else {
          control.removeClass('control-required');
        }
      }
    }

    this.triggerRenderValidate();
  },

  changeRequiredControl: function changeRequiredControl(argument) {
    this.changeRequiredLabel(argument);
  } });