// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.querySearchUser", bizagi.rendering.querySearchUser.prototype);
// Extends itself
bizagi.rendering.desktop.querySearchUser.extend("bizagi.rendering.desktop.querySearchUser", {}, {
  /*
                                                                                                   *   Update or init the element data
                                                                                                   */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);
  },

  /*
      * Template method to implement in each device to customize each render after processed
      */
  postRender: function postRender() {
    var self = this;
    // Call base
    self.input = $(".ui-bizagi-render-text", self.getControl());
    // Call base
    this._super();
  },

  /*
      *   Sets the value in the rendered control
      */
  setDisplayValue: function setDisplayValue(value) {
    if (_typeof(value) === "object") {
      if (_typeof(value[0]) === "object" && value[0].label !== undefined) {
        value = value[0].label;
      } else
      {
        value = value.label;
      }
    } else
    {
      value = '';
    }

    var self = this,
    properties = self.properties;

    // Call base
    this._super(value);

    // Set value in input
    if (!bizagi.util.isEmpty(value) && properties.editable) {
      self.input.val(value);
    }
  },

  /*
      * Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var control = self.getControl();

    self.bindSearchUserPlugin(control);
  },

  /*
      * Binds the search user plugin
      */
  bindSearchUserPlugin: function bindSearchUserPlugin(control) {
    var self = this;
    var idForm = self.getFormContainer().properties.id;

    var options = {
      title: self.getResource("render-plugin-search-users-title"),
      filters: ["domain", "userName", "fullName"],
      displayNames: [
      self.getResource("render-plugin-search-users-domain"),
      self.getResource("render-plugin-search-users-user-name"),
      self.getResource("render-plugin-search-users-full-name")],
      searchButtonName: self.getResource("render-plugin-search-users-search-button-name"),
      clearButtonName: self.getResource("render-plugin-search-users-clear-button-name"),
      valuesToSelect: ["iduser", "username"], //"iduser","username","user","domain","email"
      renderFactoryReference: self.renderFactory,
      getResourceReference: self.getResource.bind(self),
      selectItem: function selectItem() {
        var deferred = $.Deferred();
        deferred.done(function (data) {
          self.onItemSelectedSuccess(data);
        }).fail(function (error) {
          bizagi.log(error);
        });
        return deferred;
      },
      searchUsers: function searchUsers(filterData) {
        return self.dataService.getUsersListFromQueryForm($.extend(filterData, { queryFormId: idForm }));
      },
      showError: function showError(error) {
        bizagi.log(error);
      },
      showMessageBox: function showMessageBox(message) {
        bizagi.showMessageBox(bizagi.localization.getResource(message), "Bizagi", "warning");
      } };


    opt = options;

    $('#btn-get-user', control).bizagiSearchUser(options);
  },
  /*
     * returns the opt var for unit testing
     * */
  getOptions: function getOptions() {
    return opt;
  },

  onItemSelectedSuccess: function onItemSelectedSuccess(itemSelected) {
    var self = this;
    var control = self.getControl();
    $(".ui-bizagi-render-text", control).val(itemSelected.username);
    var item = {};
    item.id = itemSelected.iduser;
    item.label = itemSelected.username;
    item.value = itemSelected.username;
    self.setValue(item);
  } });