/*
*   Name: BizAgi Desktop Number Column Decorator Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the number column decorator class to adjust to desktop devices
*/

// Extends from column
bizagi.rendering.desktop.columns.column.extend("bizagi.rendering.desktop.columns.number", {}, {

  /*
                                                                                                *   Post process the element after it has been rendered
                                                                                                */
  postRender: function postRender(surrogateKey, cell) {
    // Call base
    this._super(surrogateKey, cell);

    // Apply styles to input
    var input = $("input", cell);
    this.applyColumnStyles(input);
  },
  renderSummary: function renderSummary(key, value) {
    var self = this;
    return self.renderReadOnly(key, value);
  },
  postRenderSummary: function postRenderSummary(key, value) {
    var self = this;
    self.postRenderReadOnly(key, value);
    return true;
  },
  /**
      * Apply custom overrides to each decorated instance
      * @param decorated
      */
  applyOverrides: function applyOverrides(decorated) {
    this._super(decorated);

    var originalRenderCellControl = decorated.internalRenderCellControl;

    // Override to base function
    decorated.internalRenderCellControl = function (editable, key, renderColumn, decorated) {
      var self = this;

      // Set editable
      var originalEditable = renderColumn.properties.editable;
      renderColumn.properties.editable = editable;

      $.when(renderColumn.render(key, decorated.getValue(key))).done(function (cell) {

        // Update cell content
        self.element && self.element.find("[data-bizagi-component=cellContent] > div").replaceWith(cell);

        // Call to base
        originalRenderCellControl.call(self, editable, key, renderColumn, decorated);
      });

      // Restore editable
      renderColumn.properties.editable = originalEditable;
    };
  } });