/*
*   Name: Bizagi render layout image
*   Author: Andres Fernando Munoz
*   Comments:
*   -   This script will redefine the non editable image render class to adjust to desktop devices
*/

// Extends from layoutRender desktop base
bizagi.rendering.desktop.layoutRender.extend("bizagi.rendering.desktop.layoutImage", bizagi.rendering.layoutImage.prototype);
// Extends itself
bizagi.rendering.desktop.layoutImage.extend("bizagi.rendering.desktop.layoutImage", {}, {
  /**
                                                                                           * Gets the url to get the array image
                                                                                           */

  postRender: function postRender() {

    var self = this;
    var control = self.getControl();

    if (self.value && typeof self.value.value !== "undefined" && self.value.value !== "" && self.value.value !== null) {
      //Get image of customer in binary format
      var url = self.getImageUri();
      $.ajax({
        url: url,
        type: "GET",
        dataType: "binary",
        processData: false,
        success: function success(blob) {

          if (typeof blob == "string") {
            var xhr = arguments[2];
            var contentType = xhr.getResponseHeader('Content-type');
            blob = bizagi.util.byteArrayToBlob(bizagi.util.stringToByteArray(blob), contentType);
          }

          var urlCreator = window.URL || window.webkitURL;
          var imageUrl = urlCreator.createObjectURL(blob);

          $(control).find('img').attr("src", imageUrl);
        } });

    }
  },

  getImageUri: function getImageUri() {
    var self = this;

    var templateControl = self.parent.parent;
    var renderParams = templateControl.properties.paramsRender || {};

    var map = {
      entity: self.value.guid,
      surrogateKey: self.value.surrogateKey,
      xpath: self.properties.xpath,
      width: 96,
      height: 96,
      hash: self.value.value,
      idcase: renderParams.idRender ? bizagi.context.idCase : "_",
      idworkitem: renderParams.idRender ? bizagi.context.idWorkitem : "_",
      contextxpath: renderParams.idRender ? renderParams.xpathActions : "_" };


    return self.dataService.serviceLocator.getUrl("render-entity-layout-imageByteArray").replaceMultiple(map);
  } });