/*
*   Name: Bizagi Desktop Render Money Extension
*   Author: UI Forms Team
*   Comments:
*   -   This script will redefine the money render class to adjust to desktop devices
*/

// Extends from render desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.money", bizagi.rendering.number.prototype);
// Extends itself
bizagi.rendering.desktop.money.extend("bizagi.rendering.desktop.money", bizagi.rendering.money.prototype);
// Extends itself
bizagi.rendering.desktop.money.extend("bizagi.rendering.desktop.money", {}, {

  /*
                                                                              *   Sets the value in the rendered control
                                                                              */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    var properties = self.properties;

    // Call base
    this._super(value);

    // Set value in input
    if (value !== undefined && value !== null && properties.editable) {
      self.numericInput.val(bizagi.util.numberToFixedString(value));

      // Formats the input
      self.executeFormatCurrencyPlugin();
    }
  },

  changeCellRequired: function changeCellRequired(argument) {
    this.changeRequired(argument);
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();

    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) === true && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) === true && !this.hasValue() && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }

    this.triggerRenderValidate();
  },

  /**
     * Customizes the font underline
     * It does not apply when we use the render.base component
     * @param underline
     * @param isLabelFormat
     */
  changeFontUnderline: function changeFontUnderline(underline, isLabelFormat) {
    var self = this;
    var textFormat = self.properties.textFormat || {};
    var valueFormat = self.properties.valueFormat || {};

    var labelStrikethrough = bizagi.util.parseBoolean(textFormat.strikethru) ? "line-through " : "";
    var valueStrikethrough = bizagi.util.parseBoolean(valueFormat.strikethru) ? "line-through " : "";
    var formatUnderline = bizagi.util.parseBoolean(underline) ? "underline" : "";

    var isLabelToFormat = typeof isLabelFormat != "undefined" || isLabelFormat != null ? isLabelFormat : true;
    var $formatElements = isLabelToFormat ? self.getFormattingLabel() : self.getElementsToStylized(self.getControl());
    var formatStrikeThrough = isLabelToFormat ? labelStrikethrough : valueStrikethrough;

    self.changeTextDecoration($formatElements, formatStrikeThrough + formatUnderline);
  },

  /**
      * Customizes the font Strikethrough property
      * It does not apply when we use the render.base component
      * @param strikeThrough
      * @param isLabelFormat
      */
  changeFontStrikethru: function changeFontStrikethru(strikeThrough, isLabelFormat) {
    var self = this;
    var textFormat = self.properties.textFormat || {};
    var valueFormat = self.properties.valueFormat || {};

    var labelUnderline = bizagi.util.parseBoolean(textFormat.underline) ? "underline " : "";
    var valueUnderline = bizagi.util.parseBoolean(valueFormat.underline) ? "underline " : "";
    var formatStrikeThrough = bizagi.util.parseBoolean(strikeThrough) ? "line-through" : "";

    var isLabelToFormat = typeof isLabelFormat != "undefined" || isLabelFormat != null ? isLabelFormat : true;
    var $formatElements = isLabelToFormat ? self.getFormattingLabel() : self.getElementsToStylized(self.getControl());

    var formatUnderline = isLabelToFormat ? labelUnderline : valueUnderline;

    self.changeTextDecoration($formatElements, formatUnderline + formatStrikeThrough);
  },

  /**
      * Context to apply styles
      * @param context
      * @returns {jQuery|HTMLElement}
      */
  getElementsToStylized: function getElementsToStylized(context) {
    return $("input.ui-bizagi-render-numeric, label.readonly-control", context);
  },

  /**
      * Customizes the font Strikethrough or underline property
      * @param {} $element
      * @param {} textDecoration
      * @returns {}
      */
  changeTextDecoration: function changeTextDecoration($element, textDecoration) {
    if (textDecoration === undefined) return this;
    if ($element === undefined) return this;

    textDecoration && $element.css("text-decoration", textDecoration);

    return this;
  } });