/*
 *   Name: Bizagi Desktop Render Template Label Extension
 *   Author: Mauricio Sánchez
 *   Comments:
 *   -   This script will redefine the label render class inside templates to adjust to desktop devices
 */

// Extends from layoutRender desktop base
bizagi.rendering.desktop.layoutRender.extend("bizagi.rendering.desktop.layoutLabel", bizagi.rendering.layoutLabel.prototype);
// Extends itself
bizagi.rendering.desktop.layoutLabel.extend("bizagi.rendering.desktop.layoutLabel", {}, {

  setValue: function setValue(val, triggerEvents) {
    var self = this;
    self.properties.displayName = val;
    this._super(val, triggerEvents);
    var control = self.getControl();
    if (control) {
      $(".ui-bizagi-render-label", control).text(val);
    }
  } });