/**
 *   Name: Bizagi Desktop Render Text Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the text render class to adjust to desktop devices
 */

// Extends from render desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.text", bizagi.rendering.text.prototype);
// Extends itself
bizagi.rendering.desktop.text.extend("bizagi.rendering.desktop.text", {}, {

  /**
                                                                             *   Template method to implement in each device to customize each render after processed
                                                                             */
  postRender: function postRender() {
    // Call base
    this._super();

    // Define max length of element
    if (this.properties.maxLength > 0)
    this.input.attr("maxlength", this.properties.maxLength);
  },

  /**
      *   Sets the value in the rendered control
      * @param {} value
      * @returns {}
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    var decodedValue = bizagi.util.decodeURI(value);

    value = this.validateValue(decodedValue, self.properties.maxLength);

    // Call base
    self.setValue(value);
    this._super(value);

    // Set value in input
    if (!bizagi.util.isEmpty(value) && this.properties.editable) {
      this.input && this.input.val(value);
    } else {
      if (value === null && this.properties.editable) {
        this.input && this.input.val("");
      }
    }
  },

  attachRetypeDouble: function attachRetypeDouble() {
    var self = this;
    var element = self.element;

    var input = $("input", element);
    if (!input || input.length == 0) return;

    // Apply blur handler to check value
    input.blur(function () {
      var control = $(this);
      self.retypeDouble(control);
    });
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();
    control.tooltip({ disabled: true });

    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) == true && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) == true && !this.hasValue() && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }

    this.triggerRenderValidate();
  },

  changeCellRequired: function changeCellRequired(argument) {
    this.changeRequired(argument);
  },

  retypeDouble: function retypeDouble(control) {
    var self = this;
    var currentValue = control.val();
    var isVisible = $(control).attr("type") !== "hidden" && $(control).css("display") !== "none" && $(control).css("visibility") !== "hidden";

    if (isVisible && $(this).data("newValue") == undefined) {

      if (!control.data("oldValue") || $(this).data("oldValue") === "") {
        // Check that there is something in the value or if a value has already been set
        if (currentValue === "" || currentValue === control.data("newValue")) return;

        self.createTooltip(control, currentValue, self.getResource("render-number-retype"));
      } else {
        // Check that there is something in the value
        if (currentValue === "") return;

        if (currentValue != control.data("oldValue")) {
          self.setValue("");

          self.createTooltip(control, "", self.getResource("render-text-retype-error"));
        } else {
          control.data("newValue", currentValue);
          control.data("oldValue", "");

          // Destroy tooltips
          control.tooltip("destroy");
        }
      }
    }
  },

  collectData: function collectData(renderValues) {
    var self = this;

    // Add the render value
    var xpath = self.properties.xpath;

    // replace strange character
    var value = self.getValue();

    if (value != null && typeof value !== "undefined" && typeof value !== "number") {
      value = value.replaceAll(String.fromCharCode("030"), '');
      value = value.replaceAll(String.fromCharCode("018"), '');
      value = value.replaceAll(String.fromCharCode("016"), '');
    }

    if (self.controlValueIsChanged()) {
      // Filter by valid xpaths and valid values
      if (!bizagi.util.isEmpty(xpath)) {
        if (!bizagi.util.isEmpty(value)) {
          renderValues[xpath] = value;
        } else if (value === "") {
          renderValues[xpath] = "";
        }
      }
    }

    //Validate if the input has a retype configured and check that both values matches, otherwise delete the original value
    if (!self.validateInputRetype()) {
      renderValues[self.properties.xpath] = "";
    }
  },

  /**
      * Customizes the font underline
      * It does not apply when we use the render.base component
      * @param underline
      * @param isLabelFormat
      */
  changeFontUnderline: function changeFontUnderline(underline, isLabelFormat) {
    var self = this;
    var textFormat = self.properties.textFormat || {};
    var valueFormat = self.properties.valueFormat || {};

    var labelStrikethrough = bizagi.util.parseBoolean(textFormat.strikethru) ? "line-through " : "";
    var valueStrikethrough = bizagi.util.parseBoolean(valueFormat.strikethru) ? "line-through " : "";
    var formatUnderline = bizagi.util.parseBoolean(underline) ? "underline" : "";

    var isLabelToFormat = typeof isLabelFormat != "undefined" || isLabelFormat != null ? isLabelFormat : true;
    var $formatElements = isLabelToFormat ? self.getFormattingLabel() : self.getElementsToStylized(self.getControl());
    var formatStrikeThrough = isLabelToFormat ? labelStrikethrough : valueStrikethrough;

    self.changeTextDecoration($formatElements, formatStrikeThrough + formatUnderline);
  },

  /**
      * Customizes the font Strikethrough property
      * It does not apply when we use the render.base component
      * @param strikeThrough
      * @param isLabelFormat
      */
  changeFontStrikethru: function changeFontStrikethru(strikeThrough, isLabelFormat) {
    var self = this;
    var textFormat = self.properties.textFormat || {};
    var valueFormat = self.properties.valueFormat || {};

    var labelUnderline = bizagi.util.parseBoolean(textFormat.underline) ? "underline " : "";
    var valueUnderline = bizagi.util.parseBoolean(valueFormat.underline) ? "underline " : "";
    var formatStrikeThrough = bizagi.util.parseBoolean(strikeThrough) ? "line-through" : "";

    var isLabelToFormat = typeof isLabelFormat != "undefined" || isLabelFormat != null ? isLabelFormat : true;
    var $formatElements = isLabelToFormat ? self.getFormattingLabel() : self.getElementsToStylized(self.getControl());

    var formatUnderline = isLabelToFormat ? labelUnderline : valueUnderline;

    self.changeTextDecoration($formatElements, formatUnderline + formatStrikeThrough);
  },

  /**
      * Context to apply styles
      * @param context
      * @returns {jQuery|HTMLElement}
      */
  getElementsToStylized: function getElementsToStylized(context) {
    return $("input.ui-bizagi-render-text, label.readonly-control", context);
  },

  /**
      * Customizes the font Strikethrough or underline property
      * @param {} $element
      * @param {} textDecoration
      * @returns {}
      */
  changeTextDecoration: function changeTextDecoration($element, textDecoration) {
    if (textDecoration === undefined) return this;
    if ($element === undefined) return this;

    textDecoration && $element.css("text-decoration", textDecoration);

    return this;
  } });