/**
 *   Name: Bizagi Desktop Render Readonly Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the readonly render class to adjust to desktop devices
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.readonly", bizagi.rendering.render.base.prototype);
// Extends from render Desktop base
bizagi.rendering.desktop.readonly.extend("bizagi.rendering.desktop.readonly", bizagi.rendering.readonly.prototype);
// Extends itself
bizagi.rendering.desktop.readonly.extend("bizagi.rendering.desktop.readonly", {}, {

  /**
                                                                                     * Returns the html templated element
                                                                                     * @returns {*}
                                                                                     */
  renderElement: function renderElement() {
    return this._super("render-base");
  },

  /**
      * Template method to get the label element
      * @returns {jQuery|HTMLElement}
      */
  getLabel: function getLabel() {
    return $("label", this._super());
  } });