/**
*   Name: Bizagi Desktop queryForm Extension
*   Author: UI Forms Team
 *   Comments:
*   -   This script will redefine the container class to adjust to desktop devices
*   -   Will apply a desktop form template
*/

// Extends from form desktop base
bizagi.rendering.desktop.form.extend("bizagi.rendering.desktop.queryForm", {}, {

  /*
                                                                                  *    Create a function to hide check all element in view when no contains check elements
                                                                                  */
  postRenderContainer: function postRenderContainer(container) {
    var self = this;
    var checkIncluded = [];
    var checkIncludedAll = self.container.find(".ui-bizagi-render-control-included-all");
    // Call base
    self._super(container);
    checkIncluded = self.container.find(".ui-bizagi-render-control-included");
    if (checkIncluded.length <= 0) {
      checkIncludedAll.parent().hide();
    }
  },
  /*
     *    Creates a json array with key values to send to the server
     */
  collectRenderValuesForSubmit: function collectRenderValuesForSubmit(request) {
    var self = this;
    var data = [],
    _matchedRender = function _matchedRender(renders, item) {
      var _matched = function _matched(rType, iType) {
        return rType.toUpperCase() === iType.toUpperCase();
      },
      _validateMatch = function _validateMatch(r) {
        return bizagi.util.tryCatch(
        function () {return _matched(r.properties.rangeQuery, item.searchType);},
        true);

      };

      return renders.filter(_validateMatch);
    };
    // Collect data in array
    self.collectRenderValuesQueryForm(data);

    // Mark data collected as original values
    $.each(data, function (i, item) {
      var _rendersFounded = self.getRenders(item.xpath),
      renders = _matchedRender(_rendersFounded, item);
      if (renders.length === 0) {
        renders = _rendersFounded;
      }

      item["displayHeader"] = renders[0].properties.xpath.indexOf("@") > -1 ? renders[0].properties.xpath : null;
      $.each(renders, function (i, render) {
        render.updateOriginalValue();
      });
    });
    request.parameters = data;
    return request;
  },

  getSearchParams: function getSearchParams(request) {
    var self = this;
    var $canvas = self.getValidationsContainer();
    self.validationController = new bizagi.command.controllers.validation(self, self.validations, $canvas);

    var valid = self.validationController.performValidations();
    if (valid) {
      return self.collectRenderValuesForSubmit(request);
    } else {
      self.validationController.expandNotificationBox();
      return null;
    }
  },
  /*
     *  configure Handlers for check  all column included in the response
     */
  configureHandlers: function configureHandlers() {
    var self = this;
    self._super();
    var checkIncludedAll = self.container.find(".ui-bizagi-render-control-included-all");
    checkIncludedAll.tooltip({ position: { my: "right-20 center", at: "right center" }, tooltipClass: 'bz-wp-queryForm-check-all' });
    checkIncludedAll.change(function () {
      var newValue = checkIncludedAll.is(":checked");

      var checkedChildren = function checkedChildren(target) {
        $.each(target.children, function (i, item) {
          if (item.element) {
            if (self.isControlVisible(item) && item.properties.includedInResult === true) {
              item.properties.included = newValue;
              var checkIncluded = item.element.find(".ui-bizagi-render-control-included");
              checkIncluded.prop("checked", newValue);
            }
          } else
          if (item.children) {
            checkedChildren(item);
          }
        });
      };

      checkedChildren(self);
    });
  },

  isControlVisible: function isControlVisible(control) {
    var self = this;
    if (control.parent) {
      if (control.properties.visible) {
        return self.isControlVisible(control.parent);
      } else {
        return false;
      }
    } else {
      return control.properties.visible;
    }
  } });