/**
 *   Name: BizAgi Desktop Render Check Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Check render class to adjust to desktop devices
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.check", bizagi.rendering.check.prototype);
// Extends itself
bizagi.rendering.desktop.check.extend("bizagi.rendering.desktop.check", {}, {

  /**
                                                                               * Template method to implement in each device to customize each render after processed
                                                                               */
  postRender: function postRender() {
    var $control = this.getControl();

    // Call base
    this._super();

    this.input = $control.find("label");
    this.performStyles();
  },

  postRenderReadOnly: function postRenderReadOnly() {
    var $control = this.getControl();
    var showLetters = bizagi.override.enableCheck === true && !this.properties.editable;
    this.input = $control.find("label");

    if (!showLetters) {
      this.performStyles();

      // Set as disabled
      var check = $("input", $control);
      check.attr("readonly", "readonly");
      check.attr("disabled", "disabled");
    }
  },

  /**
      * Method to render non editable values
      * @returns {jQuery|*}
      */
  renderReadOnly: function renderReadOnly() {
    var html = "";
    var showLetters = bizagi.override.enableCheck === true && !this.properties.editable;

    if (!showLetters) {
      html = this._super();
    } else {
      var template = this.renderFactory.getTemplate("readonly");
      var randomId = bizagi.util.randomNumber(100, 10000000);

      // Render template
      html = $.fasttmpl(template, {
        id: randomId,
        editable: this.properties.editable });

    }

    return html;
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    // Call base
    self._super();

    // Bind change event
    if (!bizagi.util.isIE()) {
      // PLUGIN IMPLEMENTATION
      self.input.bind("checkchange", function (evt, ui) {
        self.setValue(ui.value);
      });
    } else {
      // NON PLUGIN IMPLEMENTATION
      self.input.bind("change", function (evt, ui) {
        var value = $("input", self.input).is(":checked");

        self.setValue(value);
        self.setDisplayValue(value);
      });
    }
  },

  /**
      * Template method to implement in each device to customize the render's behaviour when rendering in design mode
      */
  configureDesignView: function configureDesignView() {
    var $control = this.getControl();

    // Unbind plugin handlers
    $control.find("input").unbind("click");
  },

  /**
      * Sets the value in the rendered control
      * @param value
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    var parsedValue = bizagi.util.parseBoolean(value);
    var showLetters = bizagi.override.enableCheck === true && !this.properties.editable;

    // Call base
    this._super(parsedValue);

    if (!showLetters) {

      if (!bizagi.util.isIE()) {
        // PLUGIN IMPLEMENTATION
        try {
          if (parsedValue === true) {
            self.input.check("checkItem");
          } else {
            self.input.check("uncheckItem");
          }
        } catch (e) {}
      } else if (self.input) {
        // NON PLUGIN IMPLEMENTATION
        if (parsedValue === true) {
          self.input.addClass("ui-checkbox-state-checked");
          $("input", self.input).attr("checked", "checked");
        } else {
          self.input.removeClass("ui-checkbox-state-checked");
          $("input", self.input).removeAttr("checked");
        }
      }
    } else if (self.input) {
      self.input.val(this.getDisplayValue());
    }
  },

  /**
      * Cleans current value
      */
  cleanData: function cleanData() {
    this.setDisplayValue(false);
    this.setValue(false, false);
  },

  /**
      * Modify styles
      */
  performStyles: function performStyles() {
    var self = this;
    var $control = self.getControl();

    // Set control container to behave as a block
    $control.addClass("ui-bizagi-render-display-block");
    $control.addClass("ui-bizagi-render-boolean-yesno");

    if (self.properties.required) {
      $(".ui-bizagi-render-control-required", $control).css({ height: "22px" });
    }

    // Apply plugins
    if (!bizagi.util.isIE()) {
      // JUST FOR NON IE-BROWSERS
      self.input.check();
    } else {
      $control.removeClass();
      $control.addClass("ui-bizagi-render-control ui-bizagi-render-boolean-yesno-ie");
      $control.find("label").addClass("checkbox-ie");
    }
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();

    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) == true && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) == true && !this.hasValue() && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }
    this.triggerRenderValidate();
  },

  changeCellRequired: function changeCellRequired(argument) {
    this.changeRequired(argument);
  },

  changeFontSize: function changeFontSize(size, isLabelFormat) {
    //this is required Just to avoid change the font size in this control
    if (isLabelFormat) {
      this._super(size, isLabelFormat);
    }
  } });