/**
 *   Name: Bizagi Desktop Render Upload Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the upload render class in order to provide a compatibility to browsers without flash installed
 */

// Extends from render base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.upload", bizagi.rendering.render.base.prototype);
// Extends from render Desktop base
bizagi.rendering.desktop.upload.extend("bizagi.rendering.desktop.upload", {
  // Statics
  BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
  BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
  BA_PAGE_CACHE: bizagi.render.services.service.BA_PAGE_CACHE },
bizagi.rendering.upload.prototype);

// Extends itself
bizagi.rendering.desktop.upload.extend("bizagi.rendering.desktop.upload", {}, {

  /**
                                                                                 * Template method to implement in each children to customize each control
                                                                                 * @returns {*|jQuery}
                                                                                 */
  renderControl: function renderControl() {
    var self = this;
    var properties = self.properties;
    var template = self.renderFactory.getTemplate("upload");

    // Render template
    var html = $.fasttmpl(template, {
      xpath: bizagi.util.encodeXpath(self.getUploadXpath()),
      editable: properties.editable,
      haveFiles: self.filesCount != 0,
      allowSendInMail: properties.allowSendInMail });


    // Render current children
    var items = "";
    for (var i = 0; i < self.filesCount; i++) {
      var file = { id: self.files[i][0], fileName: self.files[i][1] };
      var item = self.renderUploadItem(file);
      items += item;
    }

    html = self.replaceFilesHtml(html, items);
    return html;
  },

  /**
      * Returns the html templated element
      * @returns {*}
      */
  renderElement: function renderElement() {
    return this._super("render-base");
  },

  /**
      * Template method to implement in each device to customize each render after processed
      */
  postRender: function postRender() {
    var self = this;

    // Call base
    self._super();

    self.isClicked = false;
    if (self.properties.allowSendInMail) self.bindEmailDialog();

    self.checkMaxFiles();

    self.preventDefaultForAnchorTag();
  },

  postRenderReadOnly: function postRenderReadOnly() {
    var self = this;
    self._super();

    if (self.properties.allowSendInMail)
    self.bindEmailDialog();
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var control = this.getControl();

    if (control.length > 0) {
      if (this.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) === true && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) === true && !this.hasValue() && this.properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }

    this.triggerRenderValidate();
  },

  changeCellRequired: function changeCellRequired(argument) {
    this.changeRequired(argument);
  },

  /**
      * Adds a validation message to the render TODO:Remover al genomizar
      * @param message
      */
  setValidationMessage: function setValidationMessage(message) {
    this._super(message);
    var iconError = $(".ui-bizagi-render-icon-error", this.element);
    $(iconError).parent().removeClass("ui-hidden");
  },

  /**
      * Bind dialog for send email
      */
  bindEmailDialog: function bindEmailDialog() {
    var self = this;
    var control = self.getControl();
    var xpathContext = self.getXpathContext() || "";
    var xpath = self.properties.xpath;
    var template = self.properties.orientation === "rtl" ? "dialog-send-email-rtl" : "dialog-send-email";

    $(".ui-bizagi-render-upload-sendmail", control).bizagiSendEmail({
      tmpl: self.renderFactory.getTemplate(template),
      value: self.value,
      self: self,
      dataService: self.dataService,
      properties: self.properties,
      xpath: xpath,
      xpathContext: xpathContext });


  },

  bindExternalPopup: function bindExternalPopup() {
    var self = this;
    var control = self.getControl();
    var params = self.getParams();
    var properties = self.properties;
    var isInGrid = self.grid ? true : false;

    var options = {
      idCase: params.idCase || bizagi.context.idCase,
      idWorkitem: params.idWorkitem || bizagi.context.idWorkitem,
      field: properties.xpath.split(".")[properties.xpath.split(".").length - 1] };


    var xpathContext = self.getXpathContext();
    var xpath = properties.xpath;

    if (isInGrid || xpathContext !== "") {
      if (typeof xpathContext == "string") {
        $.extend(options, { xPath: xpathContext.split("[")[0] + "." + xpath });
      } else {
        $.extend(options, { xPath: self.grid.properties.xpath + "." + xpath });
      }
    } else {
      $.extend(options, { xPath: xpath });
    }

    $(".ui-bizagi-render-upload-sendmail", control).tooltip();

    $(".ui-bizagi-render-upload-sendmail", control).click(function () {
      var url = "App/Upload/SendAttachedDocs.aspx?";
      url += "idCase=" + options.idCase;
      url += "&idWorkitem=" + options.idWorkitem;
      url += "&xPath=" + options.xPath;
      url += "&field=" + options.field;

      self.dataService.getCaseNumber({
        idCase: options.idCase }).
      done(function (response) {
        var caseNumber = response.caseNumber;
        caseNumber = caseNumber ? caseNumber : options.idCase;
        url += "&radNumber=" + caseNumber;

        // fix for SUITE-8927
        var values = [];
        if (self.value) {
          for (var i = 0; i < self.value.length; i++) {
            values.push(self.value[i][1]);
          }
        }

        self.currentPopup = "genericiframe";
        $(document).triggerHandler("showDialogWidget", {
          widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_GENERICIFRAME,
          widgetURL: url,
          injectCss: [".ui-bizagi-old-render*", ".ui-bizagi-old-render-upload"],
          modalParameters: {
            title: "Send attached documents",
            id: "sendEmail" },

          afterLoad: function afterLoad(params) {
            var input = document.getElementById("btnClose");
            if (input) {
              input.style.display = "none";
            }
            input = document.getElementById("btnCloseConfirm");
            if (input) {
              input.style.display = "none";
            }

            // fix for SUITE-8927
            if (params) {
              if (params.values) {
                var values = params.values;
                var labels = document.getElementsByTagName("label");
                if (labels) {
                  for (var i = 0; i < labels.length; i++) {
                    var label = labels[i];
                    // fix for SUITE-8927
                    if (label.innerText !== undefined) {
                      if (values.indexOf(label.innerText) === -1) {
                        if (label.control) {
                          label.control.checked = false;
                          label.parentElement.style.display = "none";
                        } else {
                          label.previousSibling.checked = false;
                          label.parentElement.style.display = "none";
                        }

                      }
                    }
                    if (label.textContent !== undefined) {
                      if (values.indexOf(label.textContent) === -1) {
                        if (label.control) {
                          label.control.checked = false;
                          label.parentElement.style.display = "none";
                        } else {
                          label.previousSibling.checked = false;
                          label.parentElement.style.display = "none";
                        }
                      }
                    }
                  }
                }
              }
            }
          },
          afterLoadParams: {
            values: values } });


      });

    });

  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var control = self.getControl();
    var properties = self.properties;
    // Call base
    self._super();

    self.pluginUpload = $(".ui-bizagi-render-upload-wrapper", control).bizagiUpload({
      renderReference: this,
      dialogTemplate: self.renderFactory.getTemplate("upload.dialog"),
      properties: {
        url: properties.addUrl,
        xpath: this.getUploadXpath(),
        idRender: properties.id,
        xpathContext: self.getUploadXpathContext(),
        idPageCache: properties.idPageCache,
        idSession: properties.sessionId,
        validExtensions: properties.validExtensions,
        maxSize: properties.maxSize,
        contexttype: properties.contexttype,
        networkSpeed: networkSpeed() },

      onUploadFileCompletedCallback: self.onUploadFileCompleted,
      onChangeFile: self.onChangeFile,
      onCheckMaxSize: self.checkMaxSize,
      onValidate: self.validate,
      maxAllowedFiles: properties.maxfiles,
      uploadedFiles: self.filesCount,
      dialogTitle: self.getResource("render-upload-link-label") });


    control.off("click").on("click", ".bz-file-name", function (evt) {
      evt.preventDefault();
      var item = $(this).closest(".ui-bizagi-render-upload-item");
      self.downloadFile(item.data());
    });

    control.on("click", ".bz-btn-icn--delete", function () {
      var item = $(this).closest(".ui-bizagi-render-upload-item");
      var file = item.data();
      $.when(self.deleteUploadItem(item, file.id)).done(function () {
        // Remove item
        item.hide();

        // Detach item
        item.remove();

        // Check maxFiles
        self.checkMaxFiles();

        //Set Value
        self.setValue(self.files, false);

        // Trigger change
        self.triggerRenderChange();
      });
    });
  },

  configureHandlersReadOnly: function configureHandlersReadOnly() {
    var self = this;
    var control = self.getControl();
    control.off("click").on("click", ".bz-file-name", function (evt) {
      var item = $(this).closest(".ui-bizagi-render-upload-item");
      self.downloadFile(item.data());
    });
  },

  /**
      * Download file
      * @param file
      */
  downloadFile: function downloadFile(file) {
    var self = this;
    var properties = self.properties;

    self.dataService.getFile({
      idRender: properties.id,
      xpath: properties.xpath,
      xpathContext: self.getXpathContext(),
      idPageCache: properties.idPageCache,
      fileId: file.id,
      contexttype: properties.contexttype }).
    then(function (response) {
      return bizagi.util.handleJSONResponse(response);
    }).then(function (blobFile) {
      bizagi.util.downloadBlobFile(blobFile, file.filename);
    }).fail(function (err) {
      var form = self.getFormContainer();
      form && form.failHandler(err);
    });
  },

  /**
      * Opens a dialog in order to upload file per file
      */
  openUploadDialog: function openUploadDialog() {
    var self = this;
    var properties = self.properties;
    var doc = window.document;

    // Reset Flag
    self.isClicked = false;

    var dialogBox = self.dialogBox = $("<div class='ui-bizagi-component-loading'/>");
    dialogBox.empty();
    dialogBox.appendTo("body", doc);

    // Define buttons
    var buttons = {};

    // Select button
    buttons[self.getResource("render-upload-dialog-select")] = function (e) {
      e.stopPropagation();
      if (!self.isClicked) {
        self.isClicked = true;
        if (self.checkMaxSize() && self.checkFileTypes() && !self.maxFilesLimit()) {
          $.when(self.processUploadFile()).done(function (data) {
            self.onUploadFileCompleted(data);
            self.closeUploadDialog();
          });
        } else {
          self.isClicked = false;
        }
      }
    };

    // Cancel button
    buttons[self.getResource("text-cancel")] = function () {
      self.closeUploadDialog();
    };

    // Creates a dialog
    dialogBox.dialog({
      width: 500,
      height: 350,
      title: self.getResource("render-upload-link-label"),
      modal: true,
      buttons: buttons,
      close: function close() {
        self.closeUploadDialog();
      },
      resizeStop: function resizeStop(event, ui) {
        if (self.form) {
          self.form.resize(ui.size);
        }
      } });


    // Render template
    var dialogTemplate = self.renderFactory.getTemplate("upload.dialog");
    $.tmpl(dialogTemplate, {
      url: properties.addUrl,
      xpath: properties.xpath,
      idRender: properties.id,
      xpathContext: properties.xpathContext,
      idPageCache: properties.idPageCache,
      idSession: properties.sessionId,
      validExtensions: properties.validExtensions.replace(";", ","),
      maxSize: properties.maxSize,
      contexttype: properties.contexttype }).
    appendTo(dialogBox);
    $(".ui-bizagi-loading-message").remove();
  },

  /**
      * Process an upload file into the server
      * @returns {*}
      */
  processUploadFile: function processUploadFile() {
    var self = this;
    var defer = new $.Deferred();
    var dialogBox = self.dialogBox;
    var uploadForm = $("form", dialogBox);
    var uploadIFrame = $("iframe", uploadForm);

    // When the iframe loads the user we need to evaluate the response and close the dialog
    var timeoutFn = function timeoutFn() {
      try {
        var response = "";
        if (uploadIFrame[0].contentWindow) {
          var iframeContent = bizagi.util.isIE8() ? $(uploadIFrame[0].contentWindow.document.body) :
          $(uploadIFrame[0].contentWindow.eval("document.body"));

          response = iframeContent.text();
        }
        if (response.length > 0) {
          defer.resolve(response);
        } else {
          var timeoutForFn = setTimeout(function () {
            timeoutFn();
            clearTimeout(timeoutForFn);
          }, 50);
        }
      } catch (e) {
        // Nothing todo, just close the window and show error message
        self.closeUploadDialog();
        window.alert("Error:" + e.toString());
      }
    };

    // Execute the timed function in order to check when the response has arrived
    timeoutFn();

    // Submit the form
    uploadForm.submit();

    return defer.promise();
  },

  /**
      * Close the upload dialog
      */
  closeUploadDialog: function closeUploadDialog() {
    var self = this;
    self.isClicked = false;
    self.dialogBox.dialog("destroy");
    self.dialogBox.remove();
  },

  /**
      * Verify if control reached maximum number of files
      */
  maxFilesLimit: function maxFilesLimit() {
    return this.filesCount >= this.properties.maxfiles;
  },

  /**
      * Check if the max files condition is true to hide the upload link
      */
  checkMaxFiles: function checkMaxFiles() {
    var self = this;
    var properties = self.properties;
    var control = self.getControl();
    var uploadField = $(".ui-bizagi-render-upload-wrapper", control);

    if (self.maxFilesLimit()) {
      //Hide upload element             
      bizagi.util.cssExtended(uploadField.find("a"), "display:none!important");

      //Disable the control, to prevent further click events
      self.enabled = false;

    } else {
      // show upload element
      bizagi.util.cssExtended(uploadField.find("a"), "display:block!important");

      //Enable the control, to prevent further click events
      self.enabled = true;
    }

    //Checks if the files count is equals to 0, to show the no files dialog
    if (self.filesCount == 0) {
      $(".no-files", control).css("display", "block");
    } else {
      $(".no-files", control).hide();
    }
  },

  onChangeFile: function onChangeFile(files) {
    var self = this;
    var i = 0;
    var length;
    var file;
    var maxSize = self.properties.maxSize;

    if (files) {

      length = files.length;

      for (; i < length;) {

        file = files[i++];

        if (file.size > maxSize) {
          self.previousCheck = false;
          return;
        }
      }
    }

    self.previousCheck = true;
  },

  /**
      * Resolves default value align for each display type
      * @param displayType
      * @returns {string}
      */
  getDefaultValueAlign: function getDefaultValueAlign() {//displayType is not more important for align values 
    return "";
  },

  /**
      * Handler to process server response after a file has been uploaded
      * @param renderReference
      * @param response
      */
  onUploadFileCompleted: function onUploadFileCompleted(renderReference, response) {
    var self = renderReference;
    var control = self.getControl();
    var uploadWrapper = $(".ui-bizagi-render-upload-wrapper", control);

    // Pre-parse response because in IE sometimes the server returns the JSON enclosed with some weird <FONT> tags
    response = response.replace(/<.+>({.*})\r?\n?.*<.+>/gm, "$1");

    // Check if previous call return a valid json
    try {
      var result = JSON.parse(response);
    } catch (e) {
      // Invalid json, try to catch error response
      var errorList = [
      {
        toFind: "Maximum request length",
        message: self.getResource("render-upload-alert-maxsize").replace("{0}", self.properties.maxSize) }];



      $.each(errorList, function (key, validation) {
        if (response.indexOf(validation.toFind) >= 0 && !result) {
          result = { type: "error", message: validation.message };
        }
      });

      if (!result) {
        // Dont mapped 
        result = { type: "error", message: "Error:" + result };
      }
    }

    if (result.type != "error") {
      // Locate it before the upload wrapper
      var newItem = self.renderUploadItem({ id: result.id, fileName: result.fileName });
      $(newItem).insertBefore(uploadWrapper);

      self.processControlLayout(self.properties.valueFormat);

      // Increment counter
      self.filesCount = self.filesCount + 1;

      //Copy the files value but not its reference
      var files = self.files.slice();

      //Add the new item to this temporal array
      files.push([result.id, result.fileName]);

      // Check maxFiles
      self.checkMaxFiles();
      //Set Value
      self.setValue(files, false);


      // Trigger change
      self.triggerRenderChange();

      if (typeof self.grid != "undefined" && typeof self.grid.properties != "undefined") {
        self.grid.properties.canBeExported = true;
      }
    } else {

      //HTTP Error 404.13 is launched when the max file size configurated for iis is reached. the Request to the server is blocked 
      if (result.message === "Error:undefined" && response.indexOf("HTTP Error 404.13") > -1)
      result.message = self.getResource("render-upload-iis-maxsize");
      // Show server error
      if (!result.isMaintenanceWindow) bizagi.showMessageBox(result.message);
    }
  },

  //#region Render-Base    

  changeFontItalic: function changeFontItalic($element, italic) {

    this._super($element, italic);

    if ($element.hasClass("ui-bizagi-render-control") && bizagi.util.parseBoolean(italic)) {
      this.getElementsToStylize(control).css("padding", "0 .1em");
    }

    return this;
  },

  getElementsToStylize: function getElementsToStylize(context) {
    return $(".ui-bizagi-render-upload-item .bz-file-name, .no-files", context);
  },

  //#endregion

  /**
   * checkMaxSize
   * @param {object} renderReference
   */
  checkMaxSize: function checkMaxSize(renderReference) {
    var self = renderReference;
    var properties = self.properties;
    var dialogBox = self.dialogBox;
    var fileControl = $("input[type=file]", dialogBox).get(0);

    // Dont exist any fucking way to know file size in IEx, just skip this validation
    if (fileControl.files != undefined) {
      if (fileControl.files.length > 0) {
        if (fileControl.files[0].size === 0) {
          self.showError(self.getResource("render-upload-error-file-empty").
          replace("%s", fileControl.files[0].name));
          return false;
        }
        if (fileControl.files[0].size > properties.maxSize) {
          self.showError(self.getResource("render-upload-alert-maxsize").replace("{0}", properties.maxSize));
          return false;
        }
        return true;
      } else {
        self.showError(self.getResource("render-required-upload").replace("#label#", ""));
        return false;
      }
    } else {
      if (bizagi.util.isIE()) {
        if (fileControl.value === "" || !fileControl.value) {
          self.showError(self.getResource("render-required-upload").replace("#label#", ""));
          return false;
        } else {
          if (self.previousCheck) {
            return true;
          } else {
            if (document.documentMode > 9) {
              self.showError(self.getResource("render-upload-alert-maxsize").
              replace("{0}", properties.maxSize));
              return false;
            } else {
              return true;
            }
          }
        }
      } else {
        return true;
      }
    }
  },

  checkFileTypes: function checkFileTypes() {
    var self = this;
    var properties = self.properties;
    var dialogBox = self.dialogBox;
    var file = $("input[type=file]", dialogBox).val();

    if (properties.validExtensions && properties.validExtensions.length > 0) {
      var validExtensions = properties.validExtensions.replaceAll("*.", "").split(";");
      if (!self.stringEndsWithValidExtension(file, validExtensions, true)) {
        self.showError(self.getResource("render-upload-allowed-extensions") + properties.validExtensions);
        return false;
      }
    }
    return true;
  },

  /**
      * Additional validations
      * @param renderReference
      * @returns {boolean}
      */
  validate: function validate(renderReference) {
    var self = renderReference;
    var properties = self.properties;
    var fileControl = $("input[type=file]", self.dialogBox).get(0) || {};

    //Verifies that the file name is not repeated
    if (self.files) {
      for (var i = 0; i < self.files.length; i++) {
        var fileName = self.files[i][1];
        if (fileName.toUpperCase() == fileControl.files[0].name.toUpperCase()) {
          self.showError(self.getResource("render-upload-error-file-exists"));
          return false;
        }
      }
    }

    return true;
  },

  stringEndsWithValidExtension: function stringEndsWithValidExtension(stringToCheck, acceptableExtensionsArray, required) {
    if (required == false && stringToCheck.length == 0) return true;

    for (var i = 0; i < acceptableExtensionsArray.length; i++) {
      if (acceptableExtensionsArray[i].toLowerCase() == "*") return true;
      if (stringToCheck.toLowerCase().endsWith(acceptableExtensionsArray[i].toLowerCase())) return true;
    }

    return false;
  },

  showError: function showError(message) {
    var self = this;
    var errorContainer = $("#alert-file-upload", self.dialogBox);

    // Empty container
    errorContainer.empty();
    errorContainer.show();

    errorContainer.html(message);
  },

  /**
      * Cleans current value
      */
  cleanData: function cleanData() {
    var self = this;
    self.setValue(null);

    return $.when(self.clearDisplayValue());
  },

  /**
      * Sets the value in the rendered control
      */
  clearDisplayValue: function clearDisplayValue() {
    var self = this;
    var control = self.getControl();

    var arrayItems = control.find(".ui-bizagi-render-upload-item");
    var promises = [];

    $.each(arrayItems, function (index, value) {
      var item = $(value);
      var file = item.data();

      if (file) {
        var promise = $.when(self.deleteUploadItem(item, file.id)).done(function () {
          // Detach item
          item.remove();

          // Trigger change
          self.triggerRenderChange();
        });
        promises.push(promise);
      }
    });

    // Remove items
    arrayItems.hide();

    // Check maxFiles
    self.filesCount = 0;
    self.checkMaxFiles();

    return $.when.apply($, promises);
  },

  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    var properties = self.properties,control,item;

    if (properties.editable) {
      if (value && value.length === 0) {
        control = self.getControl();
        item = control.find(".ui-bizagi-render-upload-item");
        item.hide();
        item.remove();
        self.checkMaxFiles();
      }
    }

    self._super(value);
  },

  getLabel: function getLabel() {
    return $("label", this._super());
  } });