/**
 *   Name: Bizagi Desktop Render Letter Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the letter render class to adjust to desktop devices
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.letter", bizagi.rendering.letter.prototype);
// Extends itself
bizagi.rendering.desktop.letter.extend("bizagi.rendering.desktop.letter", {}, {

  /**
                                                                                 * Template method to implement in each device to customize each render
                                                                                 * after processed
                                                                                 */
  postRender: function postRender() {
    var button = $(":button", this.getControl());

    // Stylize button
    button.button();
  },

  /**
      * Template method to implement in each device to customize the render's
      * behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var button = $(":button", self.getControl());

    // Call base
    self._super();

    // Bind event
    button.click(function () {
      // Save the form prior opening the dialog
      $.when(self.saveForm()).done(function () {
        // Show letter dialog
        self.showLetterDialog();
      });
    });
  },

  /**
      *  Method to render non editable values
      */
  postRenderReadOnly: function postRenderReadOnly() {

  },

  /**
      * Template method to implement in each device to customize the render's behaviour
      * to add handlers when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    var self = this;
    var control = self.getControl();
    var url = self.dataService.getLetterNotEditableUrl({
      idRender: self.properties.id,
      xpath: self.getXpath(),
      xpathContext: self.getContextXpath(),
      idPageCache: self.properties.idPageCache });


    // Bind click
    $(".ui-bizagi-render-letter-readonly", control).click(function () {
      // Save the form prior opening the link
      $.when(self.saveForm()).done(function () {
        $.when(self.getCanGenerateLetter()).
        done(function (wasGenerated) {
          // Save content in server
          if (wasGenerated && bizagi.util.parseBoolean(wasGenerated.canGenerate))
            // Open the url in a new window
            window.open(url);else
          {
            bizagi.showMessageBox(bizagi.localization.getResource("render-letter-not-generated"));
          }
        });
      });
    });
  },

  /**
      * Shows letter dialog
      */
  showLetterDialog: function showLetterDialog() {
    var self = this;
    var dialog = new bizagi.rendering.dialog.letter(self.dataService, self.renderFactory);

    $.when(self.getCanGenerateLetter()).
    done(function (wasGenerated) {
      // Save content in server
      if (wasGenerated && bizagi.util.parseBoolean(wasGenerated.canGenerate)) {
        $.when(self.getLetterContent()).
        pipe(function (content) {
          return dialog.render(content);
        }).done(function (newContent) {
          // Save content in server
          self.saveLetterContent(newContent);
        });
      } else {
        bizagi.showMessageBox(bizagi.localization.getResource("render-letter-not-generated"));
      }
    });
  } });