/*
*   Name: BizAgi Desktop Text Column Decorator Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the text column decorator class to adjust to desktop devices
*/

// Extends from column
bizagi.rendering.desktop.columns.column.extend("bizagi.rendering.desktop.columns.text", {}, {

  /*
                                                                                              *   Post process the element after it has been rendered
                                                                                              */
  postRender: function postRender(surrogateKey, cell) {
    // Call base
    this._super(surrogateKey, cell);

    // Apply styles to input
    var input = $("input, textarea", cell);
    this.applyColumnStyles(input);
  },

  postRenderReadOnly: function postRenderReadOnly(surrogateKey, cell) {
    var self = this;
    var properties = self.properties;

    if (properties.autoExtend) {
      var readOnly = cell.find(".ui-bizagi-cell-readonly");
      if (readOnly.length > 0) {
        readOnly.css("max-height", "none");
      }
    }

    self._super(surrogateKey, cell);
  },

  attachRetypeDouble: function attachRetypeDouble() {
    var self = this;
    var element = self.element;

    var input = $("input", element);
    if (!input || input.length == 0) return;

    // Apply blur handler to check value
    input.blur(function () {
      var control = $(this);
      var previousValue = self.previousValue;
      var value = self.value;

      if (previousValue != null && value != previousValue) {
        if ($(this).data("newValue") != undefined && control.val() != $(this).data("newValue")) {
          $(this).data().newValue = undefined;
        }
      }
      self.retypeDouble(control);

    });
  },
  /**
     * Apply custom overrides to each decorated instance
     * @param decorated
     */
  applyOverrides: function applyOverrides(decorated) {
    this._super(decorated);

    var originalRenderCellControl = decorated.internalRenderCellControl;

    // Override to base function
    decorated.internalRenderCellControl = function (editable, key, renderColumn, decorated) {
      var self = this;

      // Set editable
      var originalEditable = renderColumn.properties.editable;
      renderColumn.properties.editable = editable;

      $.when(renderColumn.render(key, decorated.getValue(key))).done(function (cell) {
        // Update cell content
        self.element && self.element.find("[data-bizagi-component=cellContent] > div").replaceWith(cell);

        // Call to base
        originalRenderCellControl.call(self, editable, key, renderColumn, decorated);
      });

      // Restore editable
      renderColumn.properties.editable = originalEditable;
    };

  } });