/**
 *   Name: Bizagi Desktop Render Date Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Date render class to adjust to desktop devices
 */

// Extends from render base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.date", bizagi.rendering.render.base.prototype);
// Extends from render desktop base
bizagi.rendering.desktop.date.extend("bizagi.rendering.desktop.date", bizagi.rendering.date.prototype);
// Extends itself
bizagi.rendering.desktop.date.extend("bizagi.rendering.desktop.date", {}, {

  //#region Side effects functions
  /**
   * Returns the html templated element
   * @returns {*}
   */
  renderElement: function renderElement(renderTemplateName) {
    // The renderTemplateName parameter only apply to advanced queries
    return this._super(renderTemplateName || "render-base");
  },

  /**
      * Method to render non editable values
      * @returns {*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    var self = this;
    var properties = self.properties;
    var ud = bizagi.util.dateFormatter;

    var date = ud.getDateFromInvariant(self.getValue(), properties.showTime);
    var value = ud.formatDate(date, properties.fullFormat, self.i18n);

    var data = {
      value: self.getMode() !== "execution" ? properties.fullFormat : value,
      editable: properties.editable };


    var template = this.renderFactory.getTemplate("readonly");
    return $.fasttmpl(template, data);
  },

  /**
      * Translate standar date format to jquery date picker
      * @property {DOM Object} context Control
      * @returns {jQuery object} jQuery Object whith the element to stylize
      */
  getElementsToStylize: function getElementsToStylize(context) {
    return $(".bz-input-icon__input, .bz-input--readonly, .readonly-control", context);
  },

  /*
      *   Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var dateControl = self.getDateControl();
    var inputDate = $(".bz-input-icon__input", dateControl);

    self.configureMobiscollCalendar();
    self.calendarInstance = $(".bz-hidden", dateControl).mobiscroll("getInst");

    // Bind event on input date
    inputDate.on("focusInput", function (e) {
      //Para lograr la compatibilidad con las formas de inicio es necesario manejar el focus del input con validaciones para no mostrar el calendario cuando no es necesario po el autofocus de los dialog.
      if (!self.getCalendarInstance().isVisible()) {
        self.getCalendarInstance().show(false, true);
        inputDate.focus();
      }
    }).
    on("keydown", function (e) {
      if (e.keyCode == "9") self.getCalendarInstance().hide();
      var timeoutKeyDown = setTimeout(function () {
        var value = inputDate.val();
        if (bizagi.util.isEmpty(value)) {
          if (self.compareDates(value, self.getCalendarInstance().getVal()))
          self.setValueFromCalendarButtons(null, function (v) {
            self.getCalendarInstance().setVal(v, false, false, false);
            self.setValue("");
            self.setDisplayValue("");
          });
        } else {
          var fecValid = self.getNormalizeDate(
          bizagi.util.dateFormatter.getDateFromFormat(value, self.properties.fullFormat, self.i18n));

          if (fecValid == 0) {
            self.setValue("");
            self.getCalendarInstance().setVal(null);
          } else {
            if (self.compareDates(inputDate.val(), self.getCalendarInstance().getVal())) {
              self.getCalendarInstance().setVal(fecValid, false, false, false);
              self.onChangeHandler();
            }
          }
        }
        if (e.keyCode == "13") self.onChangeHandler();
        clearTimeout(timeoutKeyDown);
      }, 1);
    }).
    on("blur", function (e) {
      if (self.compareDates(inputDate.val(), self.getCalendarInstance().getVal())) self.onChangeHandler();
    });

    dateControl.click(function (e) {
      self.getCalendarInstance().show(false, true);
      inputDate.focus();
    });
  },

  /**
      * Initializes the Mobiscroll Calendar with the indicated configuration
      */
  configureMobiscollCalendar: function configureMobiscollCalendar() {
    var self = this;
    var properties = self.properties;
    self.elementCalendar = null;
    var controlElements = properties.showTime ? ["calendar", "time"] : ["calendar"];
    var dateControl = self.getDateControl();
    var fakeDiv = $(".bz-hidden", dateControl);
    var inputDate = $(".bz-input-icon__input", dateControl);
    var orientation = self.getOrientation();

    fakeDiv.mobiscroll().calendar({
      touchUi: false,
      display: "bubble",
      animate: false,
      anchor: dateControl.get(0),
      focusOnClose: inputDate.get(0),
      controls: controlElements,
      min: self.getNormalizeDate(new Date(properties.minValue)),
      max: self.getNormalizeDate(new Date(properties.maxValue)),
      defaultValue: self.getDisplayValue(),
      timeFormat: properties.timePickerFormat ? self.getTimeFormat(properties.timePickerFormat) : "",
      dateFormat: properties.datePickerFormat,
      dateWheels: 'dd/MM/yyyy',
      select: "single",
      selectType: "Day",
      rtl: orientation && orientation === "rtl",
      lang: bizagi.util.languages[BIZAGI_LANGUAGE] || bizagi.localization.language || "en",
      cssClass: "mbsc-bizagi-theme",
      buttons: self.properties.showTime ?
      [
      {
        text: self.getResource("render-timeentry-now"),
        handler: function handler() {
          self.setValueFromCalendarButtons(new Date(), function (v) {
            self.getCalendarInstance().setVal(v, false, false, false);
            self.getCalendarInstance().hide();
          });
        },
        cssClass: "mbsc-fr-btn mbsc-btnNow" },

      {
        text: self.getResource("render-plugin-search-users-select-link"),
        handler: "set",
        cssClass: "mbsc-fr-btn mbsc-btnSet" },

      {
        text: self.getResource("render-plugin-search-users-clear-button-name"),
        handler: function handler(e, inst) {
          inst.clear();
        },
        cssClass: "mbsc-fr-btn mbsc-btnClear" }] :


      [
      {
        text: self.getResource("render-timeentry-now"),
        handler: function handler() {
          self.setValueFromCalendarButtons(new Date(), function (v) {
            self.getCalendarInstance().setVal(v, false, false, false);
            self.getCalendarInstance().hide();
          });
        },
        cssClass: "mbsc-fr-btn mbsc-btnNow" },

      {
        text: self.getResource("render-plugin-search-users-clear-button-name"),
        handler: function handler(e, inst) {
          inst.clear();
        },
        cssClass: "mbsc-fr-btn mbsc-btnClear" }],


      onSet: function onSet() {
        self.onChangeHandler();
      },
      onDayChange: function onDayChange(obj) {
        self.setValueFromCalendarButtons(obj.date, function (v) {
          self.getCalendarInstance().setVal(v, false, false, false);
          if (!self.properties.showTime) self.getCalendarInstance().hide();
        });
      },
      onChange: function onChange(obj) {
        if (self.elementCalendar) {
          var c = $(".mbsc-sc-whl-anim", self.elementCalendar).length;
          if (c == 0) {
            self.setDateFromTimeSelector(obj.valueText);
          } else {
            var fnControl = setInterval(function () {
              if (!self.elementCalendar || $(".mbsc-sc-whl-anim", self.elementCalendar).length == 0) {
                self.setDateFromTimeSelector(obj.valueText);
                clearInterval(fnControl);
              }
            }, 100);
          }
        }
      },
      onClear: function onClear() {
        self.setValueFromCalendarButtons(null, function (v) {
          self.getCalendarInstance().setVal(v, false, false, false);
          self.getCalendarInstance().hide();
        });
      },
      onBeforeShow: function onBeforeShow(opt, inst) {
        inst.option({
          max: bizagi.util.dateFormatter.getDateFromInvariant(properties.maxValue, properties.showTime),
          min: bizagi.util.dateFormatter.getDateFromInvariant(properties.minValue, properties.showTime) });

      },
      onShow: function onShow(event) {
        self.elementCalendar = calendar = $(".mbsc-fr-popup", event.target);
        //Para customizar los botones del calendario
        $(".mbsc-btnNow", calendar).
        parent().
        addClass("mbsc-div-btnNow");
        $(".mbsc-btnSet", calendar).
        parent().
        addClass("mbsc-div-btnSet");
      },
      onClose: function onClose() {
        self.elementCalendar = null;
      } });

  },

  /**
      * Sets the date selected from the time selector
      * @param {String} value New value
      * @returns {}
      */
  setDateFromTimeSelector: function setDateFromTimeSelector(value) {
    var self = this;
    self.setValueFromCalendarButtons(bizagi.util.dateFormatter.getDateFromFormat(value, self.properties.fullFormat), function (v) {
      self.getCalendarInstance().setVal(v, false, false, false);
      if (!self.properties.showTime) self.getCalendarInstance().hide();
    });
  },

  /**
      * Compares two dates and and determines if they are equal
      * @param {Date} firsDate first date to compare
      * @param {Date} secondDate second date to compare
      * @returns {boolean} true o false
      */
  compareDates: function compareDates(firstDate, secondDate) {
    var self = this;
    var util = bizagi.util.dateFormatter;
    var funcNormalice = self.getNormalizeDate;
    firstDate = funcNormalice.call(
    self,
    typeof firstDate != "string" ? firstDate : util.getDateFromFormat(firstDate, self.properties.fullFormat, self.i18n));

    secondDate = funcNormalice.call(
    self,
    typeof secondDate != "string" ? secondDate : util.getDateFromFormat(secondDate, self.properties.fullFormat, self.i18n));


    if (!firstDate && !secondDate) return false;
    if (!firstDate && secondDate || firstDate && !secondDate) return true;
    if (firstDate.getTime() != secondDate.getTime()) return true;
    return false;
  },

  /**
      * Gets Mobiscroll Calendar's instance
      * @returns {Object} Mobiscroll's instance
      */
  getCalendarInstance: function getCalendarInstance() {
    return this.calendarInstance || (this.calendarInstance = $(".bz-hidden", this.getDateControl()).mobiscroll("getInst"));
  },

  /*
      *   Template method to implement in each device to customize the render's behaviour when rendering in design mode
      */
  configureDesignView: function configureDesignView() {
  },
  /*
      *   Gets the date control
      */

  getDateControl: function getDateControl() {
    return this.properties.editable ? $(".bz-input-icon, .bz-input", this.getControl()) : this.getControl();
  },

  removeValidations: function removeValidations() {
    var self = this;
    var form = self.getFormContainer();
    if (form.clearValidationMessages) {
      form.clearValidationMessages();
    } else {
      if (form.getFormContainer().parent && form.getFormContainer().parent.getFormContainer) {
        form = form.getFormContainer().parent.getFormContainer();
        if (form.clearValidationMessages) {
          form.clearValidationMessages();
        }
      }
    }
  },

  changeRequired: function changeRequired(argument) {
    this.properties.required = argument;
    this.changeRequiredLabel(argument);
  },

  changeRequiredLabel: function changeRequiredLabel(argument) {
    var self = this;
    var properties = self.properties;
    var control = self.getControl();

    if (control.length > 0) {
      if (self.getMode() !== "execution") {
        if (bizagi.util.parseBoolean(argument) == true && properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      } else {
        if (bizagi.util.parseBoolean(argument) == true && !self.hasValue() && properties.editable) {
          control.addClass("control-required");
        } else {
          control.removeClass("control-required");
        }
      }
    }
    self.triggerRenderValidate();
  },

  changeCellRequired: function changeCellRequired(argument) {
    this.changeRequired(argument);
  },
  /*
      *   Formats the date time value to the hidden control
      */
  onChangeHandler: function onChangeHandler(isNow) {
    var self = this;
    var properties = self.properties;
    var currentDate = self.getNormalizeDate(self.getCalendarInstance().getVal());
    var currentDateString = "";
    var ud = bizagi.util.dateFormatter;

    if (!currentDate || currentDate == 0) {
      return;
    }
    //Attemp set the value of the datepicker control. If not valid go back to the previous.
    var previousValue = self.value;

    //sets the value with the current format date, changed to use the same displayValue method
    if (isNow == true) {
      currentDate = self.getNormalizeDate(new Date());
      currentDateString = ud.formatDate(currentDate, "MM/dd/yyyy HH:mm:ss");
      self.value = currentDateString;
    } else {
      self.value = ud.formatInvariant(currentDate, properties.showTime);
    }
    //Set seconds in 0 when control have default format and showTime enabled
    if (properties.showTime && !properties.fullFormat.includes("s")) {
      currentDate.setSeconds(0);
      self.value = ud.formatInvariant(currentDate, properties.showTime);
    }

    if (isNow || currentDate) {
      var form = self.getFormContainer();

      if (typeof form.validationController === "undefined") {
        var $canvas = form.getValidationsContainer();
        form.validationController = new bizagi.command.controllers.validation(form, self.validations, $canvas);
      }

      self.removeValidations();
      if (!self.isValid([])) {
        var oldVal = self.getCalendarInstance().getVal();
        var message = bizagi.localization.getResource("workportal-general-invalid-date");
        message = message.replace("{0}", properties.displayName);

        var validationController = form.validationController || form.parent.validationController;
        validationController.showValidationMessage(message + " " + oldVal);
      }

      self.value = previousValue;
      currentDateString = ud.formatDate(currentDate, "MM/dd/yyyy HH:mm:ss");
      self.setValue(currentDateString);
      self.setDisplayValue(currentDateString);
    }
  },

  getLabel: function getLabel() {
    var self = this;
    if (!self.label) {
      self.label = $(".ui-bizagi-label > label", self.element || self.observableElement);
    }

    return self.label;
  },

  hideIcon: function hideIcon(icon) {
    var self = this;
    self._super(icon);

    var containerIcons = $(".ui-bizagi-render-icons", self.element);
    var errorIcon = $(".ui-bizagi-render-icon-error", self.element);
    var helpIcon = $(".ui-bizagi-render-icon-help", self.element);

    if (!helpIcon.is(":visible") && !errorIcon.is(":visible")) {
      containerIcons.css({ width: "1px", "min-width": "1px" });
    }
  },
  /**
      * Apply the final format that will be showed on the input
      */
  getFullFormatedDate: function getFullFormatedDate(currentDate, currentTime) {
    var fullDate = "";
    var ud = bizagi.util.dateFormatter;

    if (currentDate.length > 0) {
      fullDate = currentDate;
    } else if (currentDate !== undefined) {
      fullDate = ud.formatISO(currentDate, false);
    } else {
      fullDate = ud.formatISO(new Date(), false);
    }

    fullDate += currentTime;
    var dateFromFormat = ud.getDateFromFormat(fullDate, "yyyy-MM-dd H:m");

    return ud.formatDate(dateFromFormat !== 0 ? dateFromFormat : currentDate, "yyyy-MM-dd H:mm:ss");
  },

  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    if (self.getMode() !== "execution") {
      return;
    }

    var properties = self.properties;
    var dateControl = self.getControl();
    var ud = bizagi.util.dateFormatter;

    if (bizagi.util.isNumeric(value) && value !== 0) {
      value = ud.formatInvariant(new Date(value), properties.showTime);
    }

    if (value) {
      if (typeof value == "string") {
        value = self.getNormalizeDate(ud.getDateFromInvariant(value, properties.showTime));
      } else {
        value = self.getNormalizeDate(value);
      }
    }

    // Assert that the date could be parsed
    var ci = self.getCalendarInstance();
    if (properties.editable && ci.setVal) ci.setVal(value);
    self.setValue(ud.formatInvariant(value, true));
    properties.displayValue = ud.formatDate(value, properties.fullFormat, self.i18n);

    var input = properties.editable ? $(".bz-input-icon__input, .bz-input--readonly", dateControl) : $(".readonly-control", dateControl);
    // Set display value
    if (typeof properties.displayValue === "string" || typeof properties.displayValue === "number") {
      if (properties.editable) {
        input.prop("value", properties.displayValue);
      } else {
        input.html(properties.displayValue);
      }
      input.prop("title", properties.displayValue);
    }
  },

  /**
      * Sets value to the control and execute a callback function
      * @param {Date} date date to asign
      * @param {Function} callBack function to execute
      * @returns {}
      */
  setValueFromCalendarButtons: function setValueFromCalendarButtons(date, callBack) {
    this._super(date);
    if (typeof callBack === "function") callBack(date);
    this.onChangeHandler();
  },

  cleanData: function cleanData() {
    this.setValue("");
    this.setDisplayValue("");
  },

  disablePopup: function disablePopup() {
    if (this.getCalendarInstance()) this.getCalendarInstance().disable();
  }

  //#endregion Side effects functions
});