/**
 * Name: Bizagi Desktop Render Link Extension
 * Author: Andres Fernando Munoz
 * Comments:
 * -   This script will redefine the non editable link render class to adjust to desktop devices
 * -   This control is based on the link render control
 */

// Extends from layoutRender desktop base
bizagi.rendering.desktop.layoutRender.extend("bizagi.rendering.desktop.layoutLink", bizagi.rendering.layoutLink.prototype);
// Extends itself
bizagi.rendering.desktop.layoutLink.extend("bizagi.rendering.desktop.layoutLink", {}, {

  /**
                                                                                         * Template method to implement in each device to customize each render after processed
                                                                                         */
  postRender: function postRender() {
    var self = this;
    var element = self.element;

    // Call base
    self._super();
    if (element) {
      $(element).on('click', $.proxy(self.onClickElement, self));
    }
  },
  /**
      * When click over link
      */
  onClickElement: function onClickElement() {
    var self = this;
    var properties = self.properties || {};
    var args = {
      displayName: properties.displayName,
      reference: properties.reference,
      referenceType: properties.referenceType,
      xpath: properties.xpath,
      contextEvent: self.getFormContainer().getContextEvent ? self.getFormContainer().getContextEvent() : '' };

    self.triggerGlobalHandler("DATA-NAVIGATION", args);
  } });